@import "@theme/vars.scss";

.input{
    position: relative;
    box-sizing: border-box;
    background: none;
    border: none;
    outline: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    max-width: 420px;
    height: auto;
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    fill: $light-medium;
    color: $dark;
    transition: padding 0.35s ease, max-width 0.35s ease;

    @media only screen and (max-width: $bp-md) {
        font-size: 14px;
    }

    &__field{
        position: relative;
        z-index: 2;
        outline: none;
        box-sizing: border-box;
        width: 100%;
        height: 52px;
        padding: 0 15px;
        background-color: $light;
        color: inherit;
        border: 1px solid #E5E7EB;
        transition: background-color 0.35s ease 0.1s, border-color 0.35s ease, padding 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            height: 40px;
        }

        &::placeholder{
            opacity: 1;
            color: #6B7280;
            transition: opacity 0.35s ease;
        }

        // &:focus{
		//     border-color: $blue;
        // }

        &[disabled]{
            pointer-events: none;
        }
    }

    &__icon {
        pointer-events: none;
        position: absolute;
        top: 14px;
        left: 16px;
        width: 24px;
        height: 24px;
        z-index: 3;

        @media only screen and (max-width: $bp-lg) {
            top: 8px;
        }

        &--right{
            left: auto;
            right: 16px;
        }

        &--right + &--right{
            right: 50px;
        }

        &.jsInputSwap{
            cursor: pointer;
            pointer-events: all;
            transition: fill 0.35s ease;

            &.is-active{
                fill: $blue;
            }
        }
    }

    &__icon:not(.input__icon--right) ~ &__field{
        padding-left: 50px;

        @media only screen and (max-width: $bp-lg) {
            padding-left: 48px;
        }
    }

    
    &__icon--right ~ &__field{
        padding-right: 50px;

        @media only screen and (max-width: $bp-lg) {
            padding-right: 48px;
        }
    }

    &__icon--right + &__icon--right ~ &__field{
        padding-right: 84px;

        @media only screen and (max-width: $bp-lg) {
            padding-right: 80px;
        }
    }
    
    &__button{
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        width: 40px;
        height: 40px;
        right: 0;
        top: 0;
        z-index: 3;

        @media only screen and (max-width: $bp-lg) {
            width: 32px;
            height: 32px;
            right: 8px;
            top: 4px;
        }

        .icon{
            width: 24px;
            height: 24px;

            @media only screen and (max-width: $bp-lg) {
                width: 16px;
                height: 16px;
            }
        }
    }

    &__error{
        box-sizing: border-box;
        display: none;
        position: relative;
        margin-top: 4px;
        z-index: 1;
        color: $red;
        font-size: 16px;
        line-height: 28px;
        width: 100%;
        transition: transform 0.35s ease, opacity 0.35s ease, visibility 0.35s ease, margin 0.35s ease;
    }

    &--badge &__field{
        border-radius: 50px;

        &::placeholder{
            color: inherit;
        }
    }

    @media only screen and (min-width: $bp-md + 1){
        &--badge{
            max-width: 40px;
            color: transparent;
        }

        &--badge &__button{
            pointer-events: none;
        }
    
        &--badge.is-focus &__button,
        &--badge.is-filled &__button {
            pointer-events: all;
        }
    
        &--badge.is-focus,
        &--badge.is-filled {
            max-width: 308px;
            color: $dark;
        }
    }

    &--md{
    }

    
    &--md &__icon:not(.input__icon--right)  + &__field{
        padding-left: 48px;
    }

    
    &--md &__icon--right + &__field{
        padding-right: 48px;
    }

    &--md &__field{
        height: 40px;
        border-width: 0.5px;
    }

    &--md &__icon {
        top: 8px;
    }
   
 
    &--textarea{
        max-width: none;
    }

    &--textarea &__field{
        padding: 15px;
        height: 124px;
        resize: none;
    }

    &:hover &__field:not([disabled]){
        border-color: #9CA3AF;
    }

    &.is-error{

    }

    &.is-focus {
        
    }

    &.is-error &__field{
        border-color: $red;
    }

    &.is-error &__error{
        display: flex;
        animation: fadeInTop 0.35s ease forwards;
    }

    &.is-filled &__field{
        border-color: #9CA3AF;
    }

	&.is-focus &__field{
		border-color: #9CA3AF;
	}

}

.input-file {
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding-bottom: 32px;
    border-bottom: 1px solid #e5e7eb;

    &.is-upload &__content{
        display: flex;
    }

    &__input {
        display: none;
    }

    &__button {
    }

    &__content {
        box-sizing: border-box;
        display: none;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        margin-top: 32px;
        animation: fadeInTop 0.35s ease forwards;
    }

    &__name {
        font-size: 16px;
        line-height: 100%;
        color: $dark-light;
    }

    &__delete {
        fill: $dark-light;
        flex-shrink: 0;
        width: 24px;
        height: 24px;
        margin-left: 16px;
        transition: fill 0.35s ease;

        &:hover{
            fill: $blue;
        }
    }
}

