@import "@theme/vars.scss";

.grid{

    &__title{
        width: 100%;
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 24px;
            line-height: 32px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 24px;
        }
    }

    &__block{
        display: flex;
        flex-direction: column;
        margin-bottom: 48px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 32px;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__more{
        margin: 32px auto 0;
        font-size: 24px;
        
        background-color: transparent;
        color: #374151;
        fill: #374151;

        &[disabled]{
            pointer-events: none;
            opacity: 0.5;
        }

        &:hover{
            background-color: rgba(221, 205, 255, 0.1);
        }

        &:active{
            background-color: rgba(215, 199, 255, 0.5);
        }
        
        @media only screen and (max-width: $bp-xl) {
            font-size: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-top: 24px;
            margin-top: 16px;
            font-size: 18px;
            width: 100%;
            height: 40px;
            background-color: rgb(249 250 251);
            color: rgb(17 24 39);
            fill: rgb(17 24 39);
            border: 1px solid rgb(229 231 235);
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            font-size: 14px;
        }
    }

    &__list{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-auto-flow: dense;
        grid-gap: 16px;
    
        @media only screen and (max-width: $bp-md) {
            grid-template-columns: repeat(2, 1fr);
        }

        .card-date{
            grid-column: 1 / -1;

            @media only screen and (max-width: $bp-lg) {
                grid-column: span 2;
            }
        }

        div[id^="bx_incl_"]{
            grid-column: 1 / -1;
        }

        .banner{
            grid-column: 1 / -1;
            margin-top: 40px;
            margin-bottom: 40px;
    
            @media only screen and (max-width: $bp-lg) {
                margin-top: 24px;
                margin-bottom: 24px;
            }
    
            @media only screen and (max-width: $bp-md) {
                margin-top: 16px;
                margin-bottom: 16px;
            }
        }
    
        .card{
    
            &--lg{
                grid-column: span 2;
                grid-row: span 2;
    
                @media only screen and (max-width: $bp-lg) {
                    grid-column: 1 / -1;
                }
    
                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2; 
                }
            }
    
            &--md{
                grid-row: span 2;
                min-height: 392px;
                height: auto;
                grid-column: auto;
    
                @media only screen and (max-width: $bp-lg) {
                    grid-column: span 2;
                    min-height: 350px;
                }
    
                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2; 
                }
            }
    
            &--sm{
                grid-row: auto;
                grid-column: auto; 
    
                @media only screen and (max-width: $bp-lg) {
                    grid-column: span 2;
                }
    
                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2; 
                }
            }
    
            &--long{
                grid-column: 1 / -1;
                
                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2;
                }
            }
    
            &--line{
                grid-column: 1 / -1;
                
                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2;
                }
            }
        }
    
        .card-set{
            @media only screen and (max-width: $bp-lg) {
                grid-column: span 2;
            }

            &--lg{
                grid-column: 1 / -1;
                margin: 8px 0;

                @media only screen and (max-width: $bp-lg) {
                    margin: 0;
                }

                @media only screen and (max-width: $bp-md) {
                    grid-column: span 2;
                    margin: 0;
                }
            }
        }
    
        .card-place{
            grid-column: 1 / -1;
        }

        .card-project{
            grid-column: 1 / -1;
            margin: 20px 0;

            @media only screen and (max-width: $bp-lg) {
                margin: 12px 0;
                margin: 0;
                grid-column: span 2;
            }
            
            @media only screen and (max-width: $bp-md) {
                margin: 8px 0;
            }
            
            &:first-of-type{
                margin-top: 0;
            }

            &--md{
                grid-column: auto;
                margin: 0;

                @media only screen and (max-width: $bp-xl) {
                    grid-column: span 2;
                }
            }

            &--long{
                margin: 0;
            }
        }

        .card-video{
            grid-column: auto;
        }

        .card-book{
            grid-column: auto;

            @media only screen and (max-width: $bp-md) {
                grid-column: span 2;
            }

            &--long,
            &--line {
                grid-column: 1 / -1;

                @media only screen and (max-width: $bp-lg) {
                    grid-column: span 2;
                }
            }
        }

        .card-request{
            grid-column: 1 / -1;
        }

        &--books{
            grid-template-columns: repeat(12, 1fr);

            @media only screen and (max-width: $bp-xl) {
                grid-template-columns: repeat(6, 1fr);
            }

            @media only screen and (max-width: $bp-lg) {
                grid-template-columns: repeat(4, 1fr);
            }

            @media only screen and (max-width: $bp-sm) {
                grid-template-columns: repeat(2, 1fr);
            }

            .card-book{
                grid-column: span 3;
                
                @media only screen and (max-width: $bp-xl) {
                    grid-column: span 2;
                }

      
                &--long,
                &--line{
                    grid-column: 1 / -1;

                    @media only screen and (max-width: $bp-sm) {
                        grid-column: span 2;
                    }
                }
            }
        }

        &--articles{
            .card{
                &--lg{
                    grid-column: 2 / 4;
                    grid-row: span 3;
    
                    @media only screen and (max-width: $bp-lg) {
                        grid-column: span 2;
                        grid-row: span 2;
                    }
    
                    @media only screen and (min-width: $bp-md + 1) {
                        height: auto;
                    }
                }
    
                &--sm{
                    @media only screen and (max-width: $bp-lg) {
                        display: none;
                        grid-column: span 2;
    
                        &:nth-of-type(2),
                        &:nth-of-type(3) {
                            display: flex;
                        }
                    }
                }
            }
        }

        &--events{
            row-gap: 24px;

            @media only screen and (max-width: $bp-lg) {
                row-gap: 16px;
            }
        }

        &--videos{
            grid-template-columns: repeat(3, 1fr);

            @media only screen and (max-width: $bp-lg) {
                grid-template-columns: repeat(2, 1fr);
            }

            @media only screen and (max-width: $bp-md) {
                grid-template-columns: 1fr;
            }
        }

        &--authors{
            grid-template-columns: repeat(6, 1fr);

            @media only screen and (max-width: $bp-xxl) {
                grid-template-columns: repeat(5, 1fr);
            }

            @media only screen and (max-width: $bp-xl) {
                grid-template-columns: repeat(4, 1fr);
                gap: 24px;
            }

            @media only screen and (max-width: $bp-lg) {
                grid-template-columns: repeat(3, 1fr);
            }

            @media only screen and (max-width: $bp-md) {
                grid-template-columns: repeat(2, 1fr);
            }

            @media only screen and (max-width: $bp-sm) {
                grid-template-columns: 1fr;
            }
        }

        &--projects{
            grid-template-columns: repeat(6, 1fr);
            
            @media only screen and (max-width: $bp-lg) {
                grid-template-columns: repeat(4, 1fr);
            }

            @media only screen and (max-width: $bp-md) {
                grid-template-columns: repeat(2, 1fr);
            }

            .card-project{
                grid-column: 1 / -1;

                @media only screen and (max-width: $bp-lg) {
                    grid-column: span 2;
                }

                &--md{
                    grid-column: span 2;
                }

                &--long{
                    @media only screen and (max-width: $bp-lg) {
                        grid-column: 1 / -1;
                    }
                }

                &--line{
                    @media only screen and (max-width: $bp-lg) {
                        grid-column: 1 / -1;
                    }
                }
            }
        }
    }
}

