@import "@theme/vars.scss";

.artist {
    
    .section__inner{
        padding: 0 16px;

        @media only screen and (max-width: $bp-xl) {
            padding: 0;
        }
    }

    &__intro {
        margin-bottom: 40px;
    }

    &__section {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -24px 8px;

        @media only screen and (max-width: $bp-lg) {
            margin: 0;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: -8px;
        }
    }

    &__block {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        min-width: 50%;
        max-width: 100%;
        flex: 1 0 50%;
        color: $dark;
        padding: 0 24px;
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg) {
            min-width: auto;
            flex: auto;
            width: 100%;
            padding: 0;
        }
    }

    &__block-header {
        margin-bottom: 24px;
    }

    &__block-title {
        margin-bottom: 8px;

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__block-body {
        width: 100%;
    }

    &__gallery {
        height: 384px;
        width: 100%;

        @media only screen and (max-width: $bp-md) {
            height: 224px;
        }

        .slider {

            &__inner {
                width: 100%;
                height: 100%;

                &.swiper-grid .slider__slide{
                    height: calc(50% - 8px) !important;
                    width: 184px !important;
                        
                    @media only screen and (max-width: $bp-md) {
                        width: 104px !important;
                    }
                }
            }

        
            &__wrapper {
                margin: 0;
            }
        
            &__slide {
                height: 100% !important;
                width: 384px !important;

                @media only screen and (max-width: $bp-md) {
                    width: 224px !important;
                }
            }

            &__navigation {
            }
        
            &__button {
            }
        
            &__button--prev {
            }
        
            &__button-icon {
            }
        
            &__button--next {
            }
        
            &__pagination {
            }
        }

        
    }

    &__gallery-item {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__members {
        display: flex;
        flex-direction: column;
    }

    &__member {
        display: flex;
        align-items: center;
        margin-bottom: 24px;

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__member-img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 80px;
        height: 80px;
        flex-shrink: 0;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 16px;

        @media only screen and (max-width: $bp-lg) {
            width: 56px;
            height: 56px;
            
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__member-name {
    }

    &__members-more {
        height: 44px;
        font-weight: 600;
        font-size: 16px;
        line-height: 100%;
    }

    .grid{
        background-color: $light;
        padding: 24px;

        @media only screen and (max-width: $bp-lg) {
            padding: 16px;
            margin-right: -16px;
            margin-left: -16px;
        }

        .card-place{
            background-color: #fff;
        }
    }

    &__comments {
    }
}


.artist-intro {

    &__bg {
        display: flex;
        height: 300px;
        min-width: 100%;
        margin: 0 -36px;

        @media only screen and (max-width: $bp-lg) {
            margin: 0 -20px;
            height: 250px;
        }

        @media only screen and (max-width: $bp-md) {
            margin: 0 -16px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__content {
        display: flex;
        align-items: center;
        margin-top: -16px;

        @media only screen and (max-width: $bp-md) {
            margin-top: -88px;
            flex-direction: column;
        }
    }

    &__img {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: auto;
        border: 4px solid #fff;
        border-radius: 50%;
        width: 200px;
        height: 200px;
        flex-shrink: 0;
        margin-right: 24px;
        overflow: hidden;

        @media only screen and (max-width: $bp-lg) {
            width: 160px;
            height: 160px;
            margin-right: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 120px;
            height: 120px;
            margin-right: 0;
            margin-bottom: 8px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__main {
        display: flex;
        flex-direction: column;
        width: 100%;
        overflow: hidden;
        padding: 16px 0;

        @media only screen and (max-width: $bp-md) {
            align-items: center;
            padding: 0;
        }
    }

    &__title {
        color: $dark-light;

        @media only screen and (max-width: $bp-md) {
            text-align: center;
        }
    }

    &__rating {
        display: inline-flex;
        align-items: center;
        color: $dark;
        fill: #FAAD14;

        @media only screen and (max-width: $bp-md) {
            margin-top: 16px;
        }

        span{
            font-weight: 600;
            font-size: 14px;
            line-height: 22px;
            margin-right: 8px;
        }

        .icon{
            margin-right: 5px;
        }

        strong{
            font-weight: 700;
            font-size: 18px;
            line-height: 24px;
        }
    }

    &__info {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        margin: 0px -12px;
        padding: 4px 0;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            flex-direction: column;
            align-items: center;
            margin: 0;
            padding: 0;
        }
        
        &:first-child{
            margin-top: 0;
        }

        &::after{
            content: "";
            background-color: #fff;
            position: absolute;
            top: 0;
            left: 0;
            width: 2px;
            height: 100%;
            z-index: 2;
            
            @media only screen and (max-width: $bp-md) {
                content: none;
            }
        }
    }

    &__col {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        max-width: 100%;
        padding: 0 12px;
        margin-top: 20px;
        color: $dark;
        border-left: 2px solid #D1D5DB;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 0;
            align-items: center;
            text-align: center;
            border-left: 0;
        }

        &>*{
            max-width: 100%;
        }

        span{
            max-width: 100%;
            font-weight: 600;
            font-size: 18px;
            line-height: 24px;
            overflow: hidden;
            text-overflow: ellipsis;

            @media only screen and (max-width: $bp-lg) {
                font-weight: 700;
                font-size: 16px;
                line-height: 22px;
            }
        }
    }

    &__col-title {
        font-weight: 600;
        font-size: 14px;
        line-height: 22px;
        color: #6B7280;
        margin-bottom: 5px;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 8px;
            color: $dark;
        }
    }
}