@import "@theme/vars.scss";

.banner {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    background-color: $blue;
    text-decoration: none;
    color: #fff;
    height: 450px;
    width: 100%;
    overflow: hidden;
    transition: background-color 0.45s ease, color 0.45s ease;
    animation: fadeIn 0.45s ease forwards;

    @media only screen and (max-width: $bp-lg) {
        height: 350px;
        margin-right: -20px;
        margin-left: -20px;
        min-width: 100%;
        width: auto;
    }

    @media only screen and (max-width: $bp-md) {
        height: 300px;
    }

    &:hover{
        &::before{
            transform: scaleX(110%);
        }
    }

    &::before{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        background-size: 100%;
        background-position: center;
        background-repeat: no-repeat;
        background-color: inherit;
        -webkit-mask-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 1261 454' fill='none' preserveAspectRatio='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M502.5 0H0V454H393.5L502.5 0Z' fill='%23ffffff'/%3E%3Cpath d='M1260.5 344L894.5 454H1260.5V344Z' fill='%23D9D9D9'/%3E%3C/svg%3E%0A");
        mask-image:url("data:image/svg+xml,%3Csvg viewBox='0 0 1261 454' fill='none' preserveAspectRatio='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M502.5 0H0V454H393.5L502.5 0Z' fill='%23ffffff'/%3E%3Cpath d='M1260.5 344L894.5 454H1260.5V344Z' fill='%23D9D9D9'/%3E%3C/svg%3E%0A");
        transition: transform 1.25s ease;

        @media only screen and (max-width: $bp-md) {
            -webkit-mask-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 375 301' fill='none' preserveAspectRatio='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M83 0L11.5 300.5H0V0H83Z' fill='%23D9D9D9'/%3E%3Cpath d='M374.5 252.5L215.5 300.5H374.5V252.5Z' fill='%23D9D9D9'/%3E%3C/svg%3E%0A");
            mask-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 375 301' fill='none' preserveAspectRatio='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M83 0L11.5 300.5H0V0H83Z' fill='%23D9D9D9'/%3E%3Cpath d='M374.5 252.5L215.5 300.5H374.5V252.5Z' fill='%23D9D9D9'/%3E%3C/svg%3E%0A");
        }
    }

    &:hover &__img{
        transform: scale(102%);
        opacity: 0.6;
    }

    &__img {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        will-change: transform;
        opacity: 0.7;
        transition: transform 0.75s ease, opacity 0.75s ease;
    }

    &__inner {
        box-sizing: border-box;
        position: relative;
        z-index: 3;
        height: 100%;
        width: 100%;
        padding: 40px 56px;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;

        @media only screen and (max-width: $bp-xl) {
            padding: 32px 40px;
        }
    
        @media only screen and (max-width: $bp-lg) {
            padding: 24px;
        }
    }

    &__header{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__labels {
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        margin: 0 0 16px;
        max-width: 75%;
        overflow: hidden;

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
            order: 2;
            margin-bottom: 12px;
        }
    }
  
    &__label {
        position: relative;
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        height: 32px;
        padding: 0 16px;
        // border-radius: 20px;
        // border: 1px solid #E5E7EB;
        color: #E5E7EB;
        font-weight: 600;
        font-size: 16px;
        line-height: 100%;
        max-width: 100%;
        overflow: hidden;
        flex-shrink: 0;
        transition: color 0.35s ease, border-color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            height: 14px;
            font-size: 14px;
            padding: 0 8px;
        }

        &:first-of-type{
            padding-left: 0;
        }

        &:last-of-type{
            &::after{
                content: none;
            }
        }

        &:last-of-type{
            flex-shrink: 1;
        }

        span{
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 100%;
            white-space: nowrap;
        }

        &::after{
            content: "";
            display: inline-flex;
            background-color: #6B7280;
            width: 1px;
            height: 16px;
            position: absolute;
            top: 50%;
            right: 0;
            transform: translate(50%, -50%);

            @media only screen and (max-width: $bp-lg) {
                height: 12px;
            }
        }
    }
  
    &__date {
        flex-shrink: 0;
        font-weight: 600;
        margin-bottom: 16px;
    }

    &__rate{
        font-size: 32px;
        line-height: 100%;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 24px;
            margin-bottom: 12px;
        }
    }

    &__subtitle{
        font-size: 24px;
        line-height: 100%;
        margin: 0 16px 16px 0;
        max-width: 50%;

        @media only screen and (max-width: $bp-xl) {
            font-size: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 18px;
            max-width: 65%;
            margin-bottom: 12px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }

    &__title {
        font-weight: 600;
    }

    &__button{
        min-width: 200px;
        margin-top: 16px;

        @media only screen and (max-width: $bp-lg) {
            min-width: auto;
            margin-top: 12px;
        }
    }
}