@import "@theme/vars.scss";

.breadcrumbs {
    display: flex;
    max-width: 100%;
    padding: 56px 0 24px;

    @media only screen and (max-width: $bp-lg){
        padding-top: 46px;
    }

    @media only screen and (max-width: $bp-md){
        padding-top: 32px;
    }

    &__list {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        overflow-x: auto;

        @media only screen and (max-width: $bp-xl) {
            padding: 0;
        }
            
        @media only screen and (max-width: $bp-lg) {
            padding: 0 16px;
            margin: 0 -16px;
        }


        -webkit-tap-highlight-color: transparent;
        scrollbar-color: transparent transparent;
        scrollbar-width: thin;
        
        &::-webkit-scrollbar {
            height: 5px;
            width: 5px;
        }
        &::-webkit-scrollbar-track {
            background-color: transparent;
        }
        &::-webkit-scrollbar-thumb {
            background-color: transparent;
        }
    }

    &__link {
        flex-shrink: 0;
        white-space: nowrap;
        display: inline-flex;
        align-items: center;
        color: #9CA3AF;
        text-decoration: none;
        transition: color 0.35s ease;

        &::after{
            content: "/";
            display: inline-flex;
            flex-shrink: 0;
            font-size: 14px;
            line-height: 22px;
            margin: 0 8px;
        }

        &:hover{
            color: $blue;
        }

        &:last-of-type{
            color: $blue;
            pointer-events: none;

            &::after{
                display: none;
            }
        }
    }
}