@import "@theme/vars.scss";


.rating{
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    justify-content: flex-end;

    &__label{
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        width: 24px;
        height: 24px;
        fill: #FEF3C7;
        transition: fill 0.25s ease;
        margin-right: 4px;

        &:hover{
            fill: #FBD34D;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }

    &__label:hover ~ &__label{
        fill: #FBD34D;
    }

    &__input{
        display: none;
    }

    &__input:checked ~ &__label{
        fill: #FAAD14;
    }
}