@import '@theme/vars.scss';

.tumbler {
    cursor: pointer;
    display: inline-flex;
    flex-shrink: 0;

    &__input {
        display: none;
    }

    &__input:checked + &__icon{
        background-color: $blue;

        &:before{
            transform: translate(24px, -50%);
        }
    }

    &:hover &__icon{
        background-color: #9CA3AF;
    }
    
    &:hover &__input:checked + &__icon{
        background-color: $blue-medium;
    }

    &__icon {
        box-sizing: border-box;
        position: relative;
        display: flex;
        align-items: center;
        width: 44px;
        height: 22px;
        padding: 2px;
        background-color: #D1D5DB;
        border-radius: 50px;
        transition: border-color 0.35s ease, background-color 0.35s ease;


        &::before{
            content: "";
            width: 18px;
            height: 18px;
            background-color: #fff;
            border-radius: 50%;
            box-shadow: 0px 2px 4px rgba(0, 35, 11, 0.2);
            position: absolute;
            top: 50%;
            left: 0px;
            transform: translate(2px,-50%);
            will-change: transform;
            transition: transform 0.5s cubic-bezier(.77,0,.175,1), background-color 0.35s ease;
        }
    }
}