@import "@theme/vars.scss";

.calendar-events {

    .slider {
        display: flex;
        align-items: center;

        &__wrapper{
            transition-timing-function: ease !important;
        }

        &__slide {
            width: auto;
            box-sizing: border-box;
            display: flex;
            align-items: center;
        }
    
        &__button {
            width: 32px;
            height: 32px;
            margin: 0;
            background-color: transparent;
            fill: $blue;
            border-radius: 0;

            @media only screen and (max-width: $bp-lg) {
                display: none;
            }

            &:hover{
                fill: $blue;
                background-color: $light;
            }
        }
    
        &__button-icon {
            width: 32px;
            height: 32px;
        }
    }
    
    &__item{
        box-sizing: border-box;
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        padding: 0 16px;
        margin-right: 32px;
        border-radius: 50px;
        font-size: 24px;
        line-height: 100%;
        color: #8F949B;
        fill: #8F949B;
        transition: background-color 0.35s ease, color 0.35s ease 0.075s, fill 0.35s ease 0.075s;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-right: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: 8px;
        }

        &:hover{
            background-color: #E5E7EB;
            color: $blue;
            fill: $blue;
        }

        &.is-active{
            pointer-events: none;
            background-color: $blue;
            color: #E2E4ED;
            fill: #E2E4ED;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }
}