@import "@theme/vars.scss";


.like{
    box-sizing: border-box;
    cursor: pointer;
    position: absolute;
    z-index: 6;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    fill: rgba(236, 125, 66, 0);
    stroke: #fff;
    transition: stroke 0.35s ease, fill 0.35s ease, opacity 0.35s ease;

    &__icon{
        width: 100%;
        height: 100%;
    }

    &:hover{
        fill: rgba(236, 125, 66, 0.5);
    }

    &:active{
        fill: rgb(236, 125, 66);
        stroke: rgb(236, 125, 66);
        opacity: 0.8;
    }

    &.is-favorite{
        fill: #EC7D42;
        stroke: #EC7D42;

        &:hover{
            fill: #BA531A;
            stroke: #BA531A;
        }
    
        &:active{
            fill: #A23F02;
            stroke: #A23F02;
            opacity: 1;
        }
    }
}