@import "@theme/vars.scss";


.input-img{
    box-sizing: border-box;
    cursor: pointer;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 76px;
    height: 76px;
    border-radius: 50%;
    flex-shrink: 0;
    overflow: hidden;

    &:hover &__icon{
        opacity: 1;
    }

    @media only screen and (max-width: $bp-lg) {
        width: 64px;
        height: 64px;
    }

    &__field{
        display: none;
    }

    &__img{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__icon{
        display: flex;
        position: absolute;
        z-index: 2;
        top: 50%;
        left: 50%;
        width: 24px;
        height: 24px;
        fill: #fff;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: opacity 0.35s ease;
    }
}