@import "@theme/vars.scss";

.card {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 24px;
    background-color: $blue;
    text-decoration: none;
    color: $light;
    height: 392px;
    overflow: hidden;
    transition: background-color 0.45s ease, color 0.45s ease;
    animation: fadeIn 0.45s ease forwards;

    @media only screen and (max-width: $bp-xl) {
        padding: 20px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 16px;
        height: 350px;
    }

    &:hover{
        &::before{
            opacity: 1;            
        }
    }

    &:hover &__img img{
        transform: scale(103%);
    }
    
    &::before{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.24) 60.94%), rgba(19, 19, 19, 0.6);
        opacity: 0.8;
        transition: opacity 0.5s ease;
    }

    &__img {
        display: flex;
        flex-shrink: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            will-change: transform;
            transition: transform 0.75s ease;
        }
    }

    &__inner {
        box-sizing: border-box;
        position: relative;
        z-index: 3;
        height: 100%;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        overflow: hidden;
    }

    &__labels-wrapper{
        display: flex;
        align-items: center;
        flex-wrap: wrap-reverse;
        margin: 0 -4px;
    }

    &__labels {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        overflow: hidden;
        max-width: 100%;

        &--gray {
            flex-wrap: nowrap;
        }
    }
  
    &__label {
        position: relative;
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        height: 24px;
        padding: 0 8px;
        border-radius: 50px;
        border: 1px solid $orange;
        color: $orange;
        fill: $orange;
        font-weight: 600;
        font-size: 14px;
        line-height: 100%;
        margin: 0 4px 8px;
        max-width: 100%;
        overflow: hidden;
        flex-shrink: 0;
        transition: color 0.35s ease, border-color 0.35s ease;

        &:nth-last-child(1),
        &:nth-last-child(2){
            flex-shrink: 1;
        }
        &:first-of-type{
            flex-shrink: 0;
            background-color: $orange;
            color: #fff;
            fill: #fff;
        }

        .icon{
            width: 16px;
            height: 16px;
            margin-right: 5px;
        }

        span{
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 100%;
            white-space: nowrap;
        }
    }
  
    &__labels--gray &__label{
        color: #D1D5DB;
        flex-shrink: 0;
        border: 0;
        border-radius: 0;
        margin-left: 0;
        margin-right: 0;

        &:first-of-type{
            flex-shrink: 0;
            background-color: transparent;
            color: #D1D5DB;
        }

        &::after{
            content: "";
            display: inline-flex;
            width: 1px;
            height: 12px;
            background-color: #9CA3AF;
            position: absolute;
            top: 50%;
            right: 0;
            transform: translate(50%, -50%);
        }

        &:last-of-type{
            flex-shrink: 1;
            &::after{
                content: none;
            }
        }
    }

    &__date {
        margin: 0 4px 8px;
        flex-shrink: 0;
        font-weight: 600;
        font-size: 14px;
        line-height: 100%;
    }

    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        margin-bottom: 8px;
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.02em;
        transition: color 0.35s ease;
        flex-shrink: 0;
        
        @media only screen and (max-width: $bp-xl) {
            font-size: 22px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
            line-height: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
        }
    }

    &__text {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        // -webkit-line-clamp: 3;
        font-family: 'Literata';
        font-size: 16px;
        line-height: 140%;
        transition: opacity 0.35s ease;
        height: max-content;
        max-height: 70px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 22px;
            max-height: 66px;
        }
    }


    .like{
        top: 30px;
        right: 30px;

        @media only screen and (max-width: $bp-lg) {
            top: 20px;
            right: 20px;
        }
    }

    &--md .like{
        top: 20px;
            right: 20px;
    }

    &--sm .like{
        display: none;
    }

    &--long .like {
        top: 48px;
        right: 16px;
        stroke: $blue;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }

        &:active{
            stroke: $blue;
        }
    
        &.is-favorite{
            stroke: #EC7D42;
    
            &:hover{
                stroke: #BA531A;
            }
        
            &:active{
                stroke: #A23F02;
            }
        }
    }   

    &--long .like ~ &__inner &__title{
        padding-right: 40px;

        @media only screen and (max-width: $bp-lg) {
            padding-right: 0;
        }
    }
    
    &--line .like {
        display: none;
    }



    &--md &__title{
        font-size: 20px;
    }

    &--md &__labels-wrapper{
        flex-direction: column-reverse;
        align-items: flex-start;
        flex-wrap: nowrap;
    }

    &--md &__labels{
        flex-wrap: nowrap;
    }

    &--sm{
        padding: 16px;
        background-color: $light-light;
        color: $gray-blue;
        height: 188px;

        &:hover{
            background-color: $blue;
            color: $light;
        }

        &::before{
            content: none;
        }
    }

    &--sm:hover &__title{
        color: $light;
    }

    // &--sm:hover &__label{
    //     color: #fff;
    //     border-color: #fff;
    // }

    &--sm:hover &__text{
        opacity: 0.8;
    }

    &--sm &__inner{
        justify-content: flex-start;
    }

    &--sm &__title{
        font-size: 18px;
        line-height: 24px;
        color: $dark;
    }

    &--sm &__labels-wrapper{
        flex-wrap: nowrap;
    }

    &--sm &__labels{
        flex-wrap: nowrap;
    }

    &--sm &__date{
        margin-left: auto;
        padding-left: 4px;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
    }

    &--sm &__text{
        // -webkit-line-clamp: 2;
        font-size: 14px;
        line-height: 22px;

        max-height: 88px;
    }



    
    &--long{
        flex-direction: row;
        padding: 0;
        background-color: #fff;
        color: $gray-blue;
        min-height: 164px;  
        height: auto;
        padding-left: 200px;

        @media only screen and (max-width: $bp-md) {
            min-height: 88px;
            padding-left: 88px;
        }

        &:hover{
            background-color: $light;
        }

        &::before{
            content: none;
        }
    }

    &--long &__inner{
        padding: 16px;
        justify-content: flex-start;

        @media only screen and (max-width: $bp-md) {
            padding: 8px;
        }
    }

    &--long &__img{
        position: static;
        width: 200px;
        height: 100%;
        margin-right: 0;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;

        @media only screen and (max-width: $bp-md) {
            width: 88px;
        }
    }

    &--long &__title{
        font-size: 18px;
        line-height: 24px;
        color: $dark;

        @media only screen and (max-width: $bp-md) {
            -webkit-line-clamp: 2;
            margin-bottom: 0;
        }
    }

    &--long &__labels-wrapper{
        flex-wrap: nowrap;
        
        @media only screen and (max-width: $bp-md) {
            margin: 0 -2px;
        }
    }

    &--long &__labels{
        flex-wrap: nowrap;


        &--gray{
            @media only screen and (max-width: $bp-md) {
                display: none;
            }
        }
    }

    &--long &__label{
 
        @media only screen and (max-width: $bp-md) {
            padding: 0;
            border: 0;
            margin: 0 2px 4px;
            height: auto;
            &:first-of-type{
                color: $orange;
                background-color: transparent;
            }
    

            &::before{
                content: "";
                display: inline-flex;
                width: 4px;
                height: 4px;
                flex-shrink: 0;
                border-radius: 50%;
                background-color: $orange;
                margin-right: 4px;
            }
    
            &:first-of-type{
                &::before{
                    content: none;
                }
            }
        }
    }

    &--long &__labels--gray &__label{
        border-color: #6B7280;
        color: #6B7280;
    }

    &--long &__date{
        padding-left: 4px;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;

        @media only screen and (max-width: $bp-md) {
            margin: 0 2px 4px;
        }
    }

    &--long &__text{
        -webkit-line-clamp: 2;
        font-size: 14px;
        line-height: 22px;
        max-height: none;

        @media only screen and (max-width: $bp-md) {
            display: none;
        }
    }

    


    &--line{
        flex-direction: row;
        background-color: transparent;
        color: $gray-blue;
        padding: 0;
        min-height: 88px;
        padding-left: 88px;
        height: auto;

        &:hover{
            background-color: $light;
        }

        &::before{
            content: none;
        }
    }

    &--line &__inner{
        justify-content: flex-start;
        padding: 8px 8px 8px 16px;

        @media only screen and (max-width: $bp-md) {
            padding: 8px;
        }
    }

    &--line &__img{
        position: absolute;
        top: 0;
        left: 0;
        width: 88px;
        height: 100%;
    }

    &--line &__title{
        font-size: 18px;
        line-height: 24px;
        color: $dark;
        -webkit-line-clamp: 2;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 0;
        }
    }

    &--line &__labels-wrapper{
        flex-wrap: nowrap;
        margin: 0 -2px;
    }

    &--line &__labels{
        flex-wrap: nowrap;
        
        &--gray{
            display: none;
        }
    }

    &--line &__label{
        padding: 0;
        border: 0;
        margin: 0 2px 4px;
        height: auto;
        
        &:first-of-type{
            color: $orange;
            fill: $orange;
            background-color: transparent;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 8px;
        }

        &::before{
            content: "";
            display: inline-flex;
            width: 4px;
            height: 4px;
            flex-shrink: 0;
            border-radius: 50%;
            background-color: $orange;
            margin-right: 4px;
        }

        &:first-of-type{
            &::before{
                content: none;
            }
        }
    }

    &--line &__date{
        padding-left: 4px;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
        margin: 0 2px 4px;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 8px;
        }
    }

    &--line &__text{
        -webkit-line-clamp: 1;
        font-size: 14px;
        line-height: 22px;
        max-height: none;

        @media only screen and (max-width: $bp-md) {
            display: none;
        }
    }
}
