@import "@theme/vars.scss";


.card-date {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 48px;
    background-color: $blue;
    text-decoration: none;
    color: #fff;
    min-height: 300px;
    overflow: hidden;
    transition: background-color 0.45s ease, color 0.45s ease;
    animation: fadeIn 0.45s ease forwards;

    @media only screen and (max-width: $bp-xl) {
        padding: 32px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 24px;
    }

    &::after{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        background-color: #000;
        opacity: 0.7;
        transition: opacity 0.75s ease;
    }

    &:hover{
        &::after{
            opacity: 0.8;            
        }
    }

    &:hover &__img img{
        transform: scale(102%);
    }

    &__img {
        display: flex;
        flex-shrink: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            will-change: transform;
            transition: transform 0.75s ease;
        }
    }

    &__inner {
        box-sizing: border-box;
        position: relative;
        z-index: 3;
        flex-direction: column;

    }

    &__header {
        display: flex;
        align-items: center;
        padding-bottom: 16px;
        margin-bottom: 16px;
        border-bottom: 1px solid #6B7280;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__title {

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 4px;
        }
    }

    &__date {
        font-size: 24px;
        line-height: 32px;
        margin-left: 24px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 22px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-left: 0;
            font-size: 20px;
            line-height: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
        }
    }

    &__text {
        font-size: 24px;
        line-height: 140%;

        @media only screen and (max-width: $bp-xl) {
            font-size: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 18px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 16px;
        }
    }
}
