@import "@theme/vars.scss";

.card-place {
    position: relative;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: wrap;
    background-color: $light;
    text-decoration: none;
    color: $dark;
    transition: background-color 0.45s ease, color 0.45s ease;
    animation: fadeIn 0.45s ease forwards;
    padding: 24px 24px 24px 328px;

    @media only screen and (max-width: $bp-xl) {
        padding-left: 240px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 16px 16px 16px 196px;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 0 0 16px;
        flex-direction: row;
        align-items: center;
    }

    &>*{
        &:last-child{
            margin-bottom: 0;
        }
    }

    &__status{
        box-sizing: border-box;
        display: flex;
        align-items: center;
        padding: 10px 16px;
        background-color: #FEE2E2;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;  
        animation: fadeIn 0.35s ease forwards;
        
        @media only screen and (max-width: $bp-md) {
            width: 100%;
            padding: 10px;
            font-size: 14px;
            line-height: 22px;
        }

        .icon{
            margin-right: 12px;
        }

        &--red{
            background-color: #FEE2E2;
            color: #B91C1C;
            fill: #B91C1C;
            order: -1;
            margin-bottom: 16px;
        }

        &--green{
            background-color: #DCFCE7;
            color: #166534;
            fill: #166534;
            order: 3;
            margin-top: 8px;
        }

        &--yellow{
            background-color: #FEF3C7;
            color: #D97706;
            fill: #D97706;
            margin-bottom: 16px;
        }
    }

    &__actions{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        z-index: 2;

        &>*{
            margin-left: 20px;

            @media only screen and (max-width: $bp-md) {
                margin-left: 0;
            }

            &:first-child{
                margin-left: 0;
            }
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            flex-direction: column;
            align-items: flex-start;
            margin-top: 12px;
        }
    }

    &__action{
        order: 10;
        width: 100%;
        height: 40px;

        .button{
            &__icon{
                display: none;
            }
        }

        @media only screen and (min-width: $bp-md + 1) {
            flex-shrink: 0;
            padding: 0;
            width: 24px;
            height: 24px;
    
            .button{
                &__text{
                    display: none;
                }

                &__icon{
                    display: inline-flex;
                    margin-left: 0;
                }
            }
        }

        &--delete{
            fill: #DC2626;

            &:hover{
                fill: #991B1B;
            }

            @media only screen and (max-width: $bp-md) {
                background-color: #DC2626;
                color: #fff;
                fill: #fff;

                &:hover,
                &:active {

                    background-color: #991B1B;
                }
            }
        }
    }

    &__actions &__status{
        padding: 5px 8px;
        font-size: 14px;
        line-height: 14px;
        margin-top: 0;
        border-radius: 50px;

        .icon{
            width: 16px;
            height: 16px;
            margin-right: 5px;
        }

        @media only screen and (max-width: $bp-md) {
            width: auto;
            max-width: calc(100% - 32px);
            margin: 0 16px 16px;
        }
    }

    &__img-wrapper{
        display: flex;
        width: 304px;
        height: 100%;
        flex-shrink: 0;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;

        @media only screen and (max-width: $bp-xl) {
            width: 216px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 180px;
        }
        
        @media only screen and (max-width: $bp-md) {
            position: relative;
            width: 80px;
            height: 80px;
            margin-right: 16px;
            margin-bottom: 16px;
        }

        &:hover img{
            transform: scale(103%);
        }
    }
 
    &__img {
        display: flex;
        width: 100%;
        height: 100%;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            will-change: transform;
            transition: transform 0.75s ease;
        }
    }

    &__inner {
        box-sizing: border-box;
        position: relative;
        z-index: 3;
        height: 100%;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        max-width: 100%;
        overflow: hidden;
    }

    &__content{
        box-sizing: border-box;
        width: 100%;

        @media only screen and (max-width: $bp-md) {
            padding-right: 16px;
            padding-left: 16px;      
        }
    }

    &__header{
        box-sizing: border-box;
        position: relative;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        width: 100%;
        text-decoration: none;
        color: $dark;
        margin-bottom: 16px;
        padding-right: 40px;
        transition: color 0.35s ease;
        
        @media only screen and (max-width: $bp-lg) {
            padding-right: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            width: calc(100% - 112px);
            align-self: flex-start;
        }

        &:hover{
            color: $blue-light;
        }
    }

    &__title-icon{
        display: inline-flex;
        align-items: center;    
        justify-content: center;
        width: 32px;
        height: 32px;
        margin-right: 16px;
        border-radius: 50%;
        flex-shrink: 0;
        background-color: $blue;
        fill: #fff;

        .icon{
            width: 24px;
            height: 24px;
        }

        &--music{
            background-color: #FF8D51;
        }

        @media only screen and (max-width: $bp-md) {
            display: none;
        }
    }

    &__title-icon + &__title{

        @media only screen and (min-width: $bp-md + 1) {
            width: calc(100% - 48px);
        }
    }

    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        width: 100%;
        margin-bottom: 8px;
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.02em;
        flex-shrink: 0;
        margin-bottom: 4px;
        
        @media only screen and (max-width: $bp-xl) {
            font-size: 22px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
            line-height: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            -webkit-line-clamp: 2;
        }
    }

    &__subtitle{
        display: inline-flex;
        width: 100%;
        font-weight: 400;
        font-size: 16px;
        line-height: 28px;
        color: #9CA3AF;
        
        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 14px;
        }
    }

    &__list{
        display: flex;
        flex-direction: column;
        margin-bottom: 8px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 0;
        }
    }

    &__row{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: 0 -6px;
        
        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: flex-start;
        }

        &:last-of-type{
            margin-bottom: -8px;
        }
    }

    &__item{
        box-sizing: border-box;
        max-width: 100%;
        display: inline-flex;
        align-items: center;
        padding: 0 6px;
        margin-bottom: 8px;
        text-decoration: none;  
        font-weight: 600;
        font-size: 16px;
        line-height: 28px;
        fill: $orange;
        color: $dark-light;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 22px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
        }

        &:hover{
            color: $blue-light;
        }

        & > .icon{
            width: 16px;
            height: 16px;
            margin-right: 12px;

            @media only screen and (max-width: $bp-md) {
                margin-right: 8px;
            }
        }

        & > .icon + span{

            @media only screen and (max-width: $bp-lg) {
                margin-left: 0;
            }
        }

        & > span{

            @media only screen and (max-width: $bp-md) {
                margin-left: 24px;
                max-width: 100%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .button{
            padding: 0 8px;

            @media only screen and (max-width: $bp-lg) {
                height: 24px;
                padding: 0 6px;
            }
        }
        
        &::after{
            content: "";
            display: inline-flex;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: #C9C9C9;
            margin-left: 12px;

            @media only screen and (max-width: $bp-md) {
                content: none;
            }
        }

        &:last-of-type::after{
            content: none;
        }

        .select{
            width: 170px;

            @media only screen and (max-width: $bp-md) {
                width: 100%;
            }

            .options{
                max-height: 76px !important;
            }
        } 
    
    }

    &__text {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 2;
        font-family: 'Literata';
        font-size: 16px;
        line-height: 24px;
        color: $gray-blue;
        margin-top: 8px;

        @media only screen and (max-width: $bp-md) {
            display: none;
        }
    }

    &__submit{
        height: 44px;
        margin-top: 8px;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
        }
    }



    .like{
        top: 0;
        right: 0;
        stroke: $blue;

        @media only screen and (max-width: $bp-lg) {
            width: 16px;
            height: 16px;
        }

        &:active{
            stroke: $blue;
        }
    
        &.is-favorite{
            stroke: #EC7D42;
    
            &:hover{
                stroke: #BA531A;
            }
        
            &:active{
                stroke: #A23F02;
            }
        }
    }


    &--sm .like {
        display: none;
    }


    &--md {
        background-color: #fff;
        padding: 24px 16px 24px 264px;

        @media only screen and (max-width: $bp-xl) {
            padding-left: 232px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 16px 16px 16px 196px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 0 0 16px;
        }

    }

    &--md &__img-wrapper{
        width: 248px;

        @media only screen and (max-width: $bp-xl) {
            width: 216px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 180px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 80px;
            height: 80px;
        }
    }

    &--md &__title{
        font-size: 18px;
        line-height: 24px;

    }

    &--md &__subtitle{
        font-size: 14px;
        line-height: 100%;
    }

    &--md &__list{

    }

    &--md &__row{

    }

    &--md &__item{
        font-size: 14px;
        line-height: 22px;

        .button{
            height: 24px;
            font-size: 14px;
            padding: 0 6px;
        }
    }

    &--md &__text{
        font-size: 14px;
        line-height: 20px;
    }


    &--sm{
        background-color: #fff;
        color: $gray-blue;
        min-height: 88px;
        padding: 8px 8px 8px 104px;

        @media only screen and (max-width: $bp-md) {
            padding-left: 96px;
            flex-direction: column;
        }

        &::before{
            content: none;
        }
    }
    
    &--sm &__img-wrapper{
        position: absolute;
        top: 0;
        left: 0;
        width: 88px;
        height: 100%;
        margin: 0;
    }


    &--sm &__header{
        width: 100%;
        padding-right: 0;
        margin-bottom: 8px;

        @media only screen and (max-width: $bp-md) {
            padding-left: 0;
            min-height: auto;
            margin-bottom: 4px;
        }
    }

    &--sm &__title{
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 4px;
        -webkit-line-clamp: 2;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 0;
        }
    }

    &--sm &__subtitle{
        font-size: 14px;
        line-height: 100%;

        @media only screen and (max-width: $bp-md) {
            display: none;
        }
    }

    &--sm &__list{
        margin-bottom: 0;
    }

    &--sm &__item{
        font-size: 14px;
        line-height: 22px;
        overflow: hidden;
        white-space: nowrap;

        .icon{
            width: 16px;
            height: 16px;
        }
    }

    &--sm &__date{
        margin-left: auto;
        padding-left: 4px;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
    }

    &--sm &__text{
        font-size: 14px;
        line-height: 22px;
        max-height: 88px;
    }

    &--rg {
        padding: 16px 16px 16px 152px;

        @media only screen and (max-width: $bp-md) {
            padding: 0 0 16px;
        }
    }

    &--rg &__img-wrapper{
        width: 136px;

        @media only screen and (max-width: $bp-md) {
            width: 80px;
            height: 80px;
        }
    }
    
    &--rg .like{
        display: none;
    }
    
    &--rg &__status{
        @media only screen and (min-width: $bp-md + 1) {
            order: 3;
            margin-top: 8px;
            margin-bottom: 0;
        }
    }

    &--rg &__actions{
        position: absolute;
        top: 16px;
        right: 16px;

        @media only screen and (max-width: $bp-md) {
            position: static;
            order: 3;
        }

    }

    &--rg &__actions &__status{
        margin-top: 0;
        order: -1;
    }

    &--rg &__header{
        margin-bottom: 8px;
    }

    &--rg &__title{
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 0;
    }

    &--rg &__subtitle{
        order: -1;
        font-size: 14px;
        line-height: 22px;
    }
    
    &--rg &__list{
        margin-bottom: 8px;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 0;
        }
    }

    &--rg &__row{
        @media only screen and (max-width: $bp-md) {
            flex-direction: row;
        }
    }

    &--rg &__item{
        font-size: 14px;
        line-height: 22px;

        @media only screen and (max-width: $bp-md) {
            width: auto;
        }
    }

    &--rg &__text{
        font-size: 14px;
        line-height: 22px;
    }



}
