@import '@theme/vars.scss';

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}

p{
    font-weight: 400;
}


.h1, .h2, .h3, .h4, .h5, .h6 {
    font-family: 'Mont';
    font-weight: 600;
}

.h1{
    font-size: 64px;
    line-height: 110%;
    letter-spacing: -0.02em;

    @media only screen and (max-width: $bp-xl) {
        font-size: 52px;
    }

    @media only screen and (max-width: $bp-lg) {
        font-size: 40px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 28px;
    }
}

.h2{
    font-size: 48px;
    line-height: 55px;
    letter-spacing: -0.02em;

    @media only screen and (max-width: $bp-lg) {
        font-size: 35px;
        line-height: 40px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 24px;
        line-height: 32px;
    }
}

.h3{
    font-size: 34px;
    line-height: 125%;
    letter-spacing: -0.02em;

    @media only screen and (max-width: $bp-lg) {
        font-size: 30px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 24px;
        line-height: 32px;
    }
}

.h4{
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.02em;

    @media only screen and (max-width: $bp-lg) {
        font-size: 22px;
        line-height: 27px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 20px;
        line-height: 25px;
    }
}

.h5{
    font-size: 20px;
    line-height: 135%;
    letter-spacing: -0.02em;

    @media only screen and (max-width: $bp-lg) {
        font-size: 18px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 16px;
    }
}

.text{
    font-size: 16px;
    line-height: 135%;

    @media only screen and (max-width: $bp-lg) {
        font-size: 14px;
    }

    &-xl{
        font-size: 20px;
        line-height: 135%;
    }

    &-lg{
        font-size: 18px;
        line-height: 135%;
    }

    &-md{
        font-size: 14px;
        line-height: 135%;
    }

    &-sm{
        font-size: 12px;
        line-height: 140%;
    }

    //STYLE

    &-border{
        -webkit-text-stroke: 1px #8C8C8C;
    }

    //WEIGHT

    &-200{
        font-weight: 200;
    }

    &-300{
        font-weight: 300;
    }

    &-400{
        font-weight: 400;
    }
    
    &-500{
        font-weight: 500;
    }

    &-600{
        font-weight: 600;
    }

    &-700{
        font-weight: 700;
    }

    &-800{
        font-weight: 800;
    }

    &-900{
        font-weight: 900;
    }

    //FAMILY


    &-mont{
        font-family: 'Mont';
    }

    &-literata{
        font-family: 'Literata';
    }
}