@import "@theme/vars.scss";

.radio {
    cursor: pointer;
    display: flex;

    &__input {
        display: none;
    }

    &__input:checked + &__wrapper &__icon-wrapper{
        border-color: $blue;
    }

    &__input:checked + &__wrapper &__icon{
        opacity: 1;
        background-color: $blue;
        transform: scale(1);
    }

    &:hover &__icon-wrapper{
        border-color: $blue;
    }

    &__wrapper {
        display: flex;
        align-items: flex-start;
    }

    &__icon-wrapper {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 25px;
        height: 25px;
        flex-shrink: 0;
        background-color: #fff;
        border-radius: 50%;
        border: 1px solid #AEBDCB;
        transition: border-color 0.35s ease, background-color 0.35s ease;
    }

    &__icon {
        width: 16px;
        height: 16px;
        opacity: 0;
        background-color: #AEBDCB;
        border-radius: 50%;
        will-change: transform;
        transform: scale(1.5625);
        transition: opacity 0.35s ease, background-color 0.35s ease, transform 0.25s cubic-bezier(.77,0,.175,1) 0.1s;
    }

    &__content {
        display: flex;
        margin: auto 0 auto 5px;
        
        span{
            display: inline;

            .tooltip-trigger{
                display: inline;
                margin-left: 5px;
            }
        }

        
        .icon + span{
            margin-left: 5px;
        }

        span + .icon{
            margin-left: 5px;
        }
    }

    &--square {
        
    }

    &--square &__icon-wrapper{
        border-radius: 0;
        width: 40px;
        height: 40px;
        border-width: 2px;
        border: 1px solid transparent ;
    }

    &--square:hover &__icon-wrapper{
        border-color: #AEBDCB;
    }

    &--square &__icon{
        width: 24px;
        height: 24px;
        border-radius: 0%;
        background-color: transparent !important;
        transform: none;
        box-shadow: none;
        opacity: 1;
    }
    
    &--square &__content{

    } 
}