@mixin font($name, $fileName, $weight) {
    @font-face {
        font-family: $name;
        src: local("#{$name}"),
            url('@fonts/#{$name}/#{$fileName}.otf') format('otf'),
            url('@fonts/#{$name}/#{$fileName}.ttf') format('ttf'),
            url("@fonts/#{$name}/#{$fileName}.woff2") format("woff2"),
            url("@fonts/#{$name}/#{$fileName}.woff") format("woff");
        font-weight: $weight;
        font-style: normal;
        font-display: swap;
    }
}
  

// Template

@include font("Mont", "Mont-Thin", 200);
@include font("Mont", "Mont-Light", 300);
@include font("Mont", "Mont-Regular", 400);
// @include font("Mont", "Mont-Medium", 500);
@include font("Mont", "Mont-Semibold", 600);
@include font("Mont", "Mont-Bold", 700);
@include font("Mont", "Mont-Black", 800);
@include font("Mont", "Mont-Heavy", 900);