@import "@theme/vars.scss";

.card-request {
    box-sizing: border-box;
    padding: 16px;
    width: 100%;
    background-color: #fff;

    &__inner {
        position: relative;
        display: flex;
        flex-wrap: wrap;

        @media only screen and (max-width: $bp-xl) {
            justify-content: space-between;
        }
    }

    &__main {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        padding-left: 92px;
        flex: 1 0 28%;
        margin-right: 35px;

        @media only screen and (max-width: $bp-xl) {
            flex: initial;
            width: calc(50% - 16px);
            margin-right: 0;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: center;
            padding-left: 0;
            margin-bottom: 16px;
        }
    }

    &__img-wrapper {
        position: absolute;
        top: 16px;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 76px;
        height: 76px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-md) {
            width: 88px;
            height: 88px;
            position: relative;
            top: 0;
            margin-right: 12px;
            margin-bottom: 16px;
        }
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__img-icon{
        position: absolute;
        top: 0;
        right: 0;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        width: 32px;
        height: 32px;
        background-color: $blue;
        fill: #fff;

        .icon{
            width: 16px;
            height: 16px;
        }
    }

    &__main-col {

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 16px;
            display: flex;
            flex-direction: column;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        width: 100%;
        font-size: 16px;
        line-height: 24px;
        font-weight: 700;
        color: $dark;
        margin-bottom: 12px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 22px;
            margin-bottom: 16px;
        }

        &:last-child{
            margin-bottom: 0;
        }

        & > span{
            font-weight: 600;
            font-size: 14px;
            line-height: 22px;
            color: $light-medium;
            margin-bottom: 5px;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 0;
            }
        }

        span{
            max-width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    &__rating {
        display: flex;
        align-items: center;
        position: absolute;
        top: 0;
        right: 0;
        opacity: 0.8;
        color: $dark;
        fill: #FAAD14;

        @media only screen and (max-width: $bp-md) {
            position: relative;
            margin-top: -12px;
        }

        span{
            font-size: 14px;
            line-height: 22px;
            color: #6B7280;
            margin-right: 8px;
        }

        strong{
            font-weight: 700;
            font-size: 18px;
            line-height: 24px;
            margin-left: 5px;   
        }
    }

    &__col {
        flex: 1 0 28%;
        display: flex;
        flex-direction: column;
        margin-right: 35px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 0;
            flex: initial;
            width: calc(50% - 16px);
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        padding-top: 40px;
        flex-grow: 1;
        margin-left: auto;

        @media only screen and (max-width: $bp-xl) {
            flex-direction: row;
            width: 100%;
            padding-top: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;

        }
    }

    &__actions {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-wrap: wrap;

        @media only screen and (max-width: $bp-xl) {
            order: 1;
            margin-bottom: -10px;
            margin-left: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-left: 0;
            margin-bottom: 0;
        }
    }

    &__action {
        margin: 0 0 16px 10px;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            height: 40px;
            margin: 8px 0 0;
        }
        
        &:first-child{
            margin-left: 0;
        }
    }

    &__list {
        display: flex;  
        align-items: center;    
        justify-content: flex-end;
        flex-wrap: wrap;
        margin: 0 -5px -10px;

        @media only screen and (max-width: $bp-md) {
            justify-content: flex-start;
            margin-bottom: 14px;
        }
    }

    &__list-item {
        margin: 0 5px 10px;
        display: inline-flex;   
        align-items: center;
        color: $dark;
        fill: $orange;
        font-weight: 600;
        font-size: 14px;
        line-height: 22px;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 10px;
        }

        .icon{
            width: 16px;
            height: 16px;
            margin-right: 4px;
        }

        span{

        }
    }
}