@import "@theme/vars.scss";

.card-video {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    background-color: transparent;
    text-decoration: none;
    color: $dark;
    overflow: hidden;
    transition: background-color 0.45s ease, color 0.45s ease;
    animation: fadeIn 0.45s ease forwards;

    &:hover{
        background-color: $light;
    }

    &:hover &__img img {
        transform: scale(104%);
    }

    &__img-wrapper {
        position: relative;
        display: flex;
        width: 100%;
        height: 228px;
        aspect-ratio: 16 / 9;
    }

    &__type {
        top: 10px;
        left: 10px;
        border-radius: 10px;
    }

    &__time{
        bottom: 10px;
        right: 10px;
    }

    &__time,
    &__type {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 8px;
        height: 24px;
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;

        z-index: 1;
        font-size: 14px;
        line-height: 14px;
        font-weight: 600;
        color: #fff;
    }

    &__img {
        width: 100%;
        height: 100%;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            will-change: transform;
            transition: transform 0.5s ease;
        }
    }

    &__inner {
        box-sizing: border-box;
        position: relative;
        z-index: 3;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        border: 1px solid $light-light;
        border-top: 0;
        padding: 8px;
        overflow: hidden;
    }

    &__main {
        display: flex;
    }

    &__author {
        display: block;
        width: 48px;
        height: 48px;
        border-radius: 50px;
        flex-shrink: 0;
        margin-right: 10px;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__info {
        display: block;
    }

    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 2;
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 8px;
    }

    &__labels {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    &__label {
        display: inline-flex;
        align-items: center;
        font-size: 14px;
        line-height: 22px;
        font-weight: 600;
        color: $orange;
        margin-right: 4px;

        &:last-of-type{
            margin-right: 0;
        }

        &::before{
            content: "";
            display: inline-flex;
            width: 4px;
            height: 4px;
            flex-shrink: 0;
            border-radius: 50%;
            background-color: $orange;
            margin-right: 4px;
        }

        &:first-of-type{
            &::before{
                content: none;
            }
        }
    }

    &__text {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        margin-top: 8px;
        padding-top: 8px;
        border-top: 1px solid #D1D5DB;
        color: $gray-blue;
        font-size: 14px;
        line-height: 20px;
        font-family: 'Literata';
    }
}