@import "@theme/vars.scss";

.card-set {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 24px;
    text-decoration: none;
    color: #FAFAFA;
    min-height: 210px;
    padding: 24px;
    overflow: hidden;
    animation: fadeIn 0.45s ease forwards;

    @media only screen and (max-width: $bp-lg) {
        height: 160px;
    }

    &--dark{
        color: #1F1F1F;
    }

    &--lg{
        min-height: 350px;
        padding: 32px;

        @media only screen and (max-width: $bp-lg) {
            padding: 24px;
            min-height: 300px;
        }
    }

    &--lg &__inner{
        max-width: 800px;

        @media only screen and (max-width: $bp-lg) {
            justify-content: flex-start;
            max-width: 600px;
        }

        @media only screen and (max-width: $bp-md) {
            max-width: none;
        }
    }

    &--lg &__title{
        font-size: 60px;
        line-height: 120%;
        margin: 8px 0 24px;

        @media only screen and (max-width: $bp-xxl) {
            font-size: 50px;
        }

        @media only screen and (max-width: $bp-xl) {
            font-size: 42px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin: 0 0 16px;
            font-size: 34px;
        }

        @media only screen and (max-width: $bp-md) {
        }
    }

    &--lg &__subtitle{
        font-weight: 600;
        margin-bottom: auto;
        font-size: 24px;
        line-height: 32px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 20px;
            line-height: 28px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-weight: 400;
            margin-bottom: 8px;
            font-size: 18px;
            line-height: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 16px;
        }
    }

    &--lg &__text{
        display: flex;
    }

    &__text{
        display: none;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 4;
        font-size: 24px;
        line-height: 32px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 20px;
            line-height: 28px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
            line-height: 24px;
            -webkit-line-clamp: 6;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
            line-height: 22px;
        }
    }

    &:hover{
     
    }

    &:hover &__img img{
        transform: scale(103%);
    }

    &__img {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top right;
            will-change: transform;
            transition: transform 0.75s ease;
        }
    }

    &__inner{
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        position: relative;
        z-index: 2;
        flex-grow: 1;
        height: 100%;
    }

    &__subtitle {
        line-height: 100%;
        margin-bottom: 8px;
    }

    &__title {
        font-weight: 700;
        font-size: 28px;
        line-height: 100%;

        @media only screen and (max-width: $bp-xl) {
            font-size: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
        }
    }
}