@import "@theme/vars.scss";

.card-project {
    cursor: pointer;
    position: relative;
    box-sizing: border-box;
    display: flex;
    background-color: $light;
    text-decoration: none;
    color: $blue;
    fill: $blue;
    overflow: hidden;
    transition: background-color 0.45s ease, color 0.45s ease;
    animation: fadeIn 0.45s ease forwards;

    @media only screen and (max-width: $bp-lg) {
        min-height: 600px;
        color: #fff;
        fill: #fff;
        background-color: $blue;

        &:hover &__button{
            background-color: $light;
            border-color: $light;
            color: $dark;
        }
    }

    @media only screen and (max-width: $bp-md) {
        min-height: 400px;
    }

    &::after{
        pointer-events: none;
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        background-color: $blue;
        opacity: 0.5;
        transition: opacity 0.5s ease;

        @media only screen and (max-width: $bp-lg) {
            content: "";
        }
    }

    &:hover{
        background-color: $light-light;

        &::after{
            opacity: 0.6;            
        }
    }

    &__inner {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        max-width: 100%;
        height: 100%;
        flex-grow: 1;
        overflow: hidden;
        padding: 48px;

        @media only screen and (max-width: $bp-xl) {
            padding: 36px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 24px;
        }
    
        @media only screen and (max-width: $bp-md) {
            padding: 16px;
        }

        &>*:last-child{
            margin-bottom: 0;
        }
    }

    &__img-block + &__inner &__header{
        justify-content: flex-end;
    }

    &__header{
        box-sizing: border-box;
        position: relative;
        z-index: 3;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        width: 100%;
        margin-bottom: 36px;

        @media only screen and (max-width: $bp-lg) {
            flex-wrap: wrap;
            justify-content: flex-end;
        }

        &>*{
            margin-bottom: 12px;

            &::before{
                content: "";
                pointer-events: none;
                display: inline-flex;
                align-items: center;
                width: 1px;
                height: 28px;
                margin: 0 8px;
                background-color: #D1D5DB;

                @media only screen and (max-width: $bp-lg) {
                    content: none;
                }
            }

            &:first-child{
                &::before{
                    content: none;
                }
            }
        }
    }
    
    &__labels{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    &__label{
        display: inline-flex;
        align-items: center;
        font-weight: 600;
        font-size: 16px;
        line-height: 28px;
        margin-right: 8px;
        
        &:last-of-type{
            margin-right: 0;
        }

        .icon{
            width: 16px;
            height: 16px;
            margin-right: 5px;
        }
    }

    &__date{
        display: inline-flex;
        align-items: center;
        font-weight: 600;
        font-size: 16px;
        line-height: 28px;

        @media only screen and (max-width: $bp-lg) {
            margin-left: auto;
            padding-left: 8px;
        }
    }

    .like{
        width: auto;
        height: auto;
        position: static;
        stroke: $blue;

        .icon{
            width: 24px;
            height: 24px;
        }

        &:active{
            stroke: $blue;
        }
    
        &.is-favorite{
            stroke: #EC7D42;
    
            &:hover{
                stroke: #BA531A;
            }
        
            &:active{
                stroke: #A23F02;
            }
        }

        @media only screen and (max-width: $bp-lg) {
            fill: rgba(236, 125, 66, 0);
            stroke: #fff;
            
            &:hover{
                fill: rgba(236, 125, 66, 0.5);
            }
        
            &:active{
                fill: rgb(236, 125, 66);
                stroke: rgb(236, 125, 66);
                opacity: 0.8;
            }
        
            &.is-favorite{
                fill: #EC7D42;
                stroke: #EC7D42;
        
                &:hover{
                    fill: #BA531A;
                    stroke: #BA531A;
                }
            
                &:active{
                    fill: #A23F02;
                    stroke: #A23F02;
                    opacity: 1;
                }
            }

            &::before{
                @media only screen and (max-width: $bp-lg) {
                    content: "";
                }
            }
        }
    }

    &--blue .like{
        fill: rgba(236, 125, 66, 0);
        stroke: #fff;
        
        &:hover{
            fill: rgba(236, 125, 66, 0.5);
        }
    
        &:active{
            fill: rgb(236, 125, 66);
            stroke: rgb(236, 125, 66);
            opacity: 0.8;
        }
    
        &.is-favorite{
            fill: #EC7D42;
            stroke: #EC7D42;
    
            &:hover{
                fill: #BA531A;
                stroke: #BA531A;
            }
        
            &:active{
                fill: #A23F02;
                stroke: #A23F02;
                opacity: 1;
            }
        }
    }

    &__inner + &__img-block &__logo{
        right: auto;
        left: auto;
        right: 48px;
    }

    &__inner + &__img-block &__img{
        clip-path: polygon(18% 0, 100% 0, 100% 82%, 0 100%);
    }

    &__img-block{
        pointer-events: none;
        position: relative;
        width: 570px;
        height: auto;
        display: flex;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xxl) {
            width: 500px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 470px;
        }
        
        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
 
    &__img {
        pointer-events: none;
        display: flex;
        width: 100%;
        height: 568px;  
        max-height: calc(100% - 64px);
        overflow: hidden;
        clip-path: polygon(0 0, 82% 0, 100% 100%, 0 82%);
        transition: clip-path 0.65s ease;

        @media only screen and (max-width: $bp-xxl) {
            height: 520px;
        }

        @media only screen and (max-width: $bp-xl) {
            height: 470px;
            max-height: calc(100% - 48px);
        }

        @media only screen and (max-width: $bp-lg) {
            clip-path: none !important;
            height: 100%;
            max-height: none;
        }


        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            will-change: transform;
            transition: transform 0.9s ease;
        }
    }

    &:hover &__img{
        clip-path: polygon(0 0, 79% 0, 100% 100%, 0 79%);

        @media only screen and (max-width: $bp-lg) {
            clip-path: none;
        }

        img{
            transform: scale(108%);

            @media only screen and (max-width: $bp-lg) {
                transform: scale(104%);
            }
        }
    }
    
    &:hover &__inner + &__img-block &__img{
        right: auto;
        left: auto;
        right: 48px;
        clip-path: polygon(21% 0, 100% 0, 100% 79%, 0 100%);
    }

    &__logo{
        position: absolute;
        height: 48px;
        width: 196px;
        bottom: 48px;
        left: 0;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__main{
        position: relative;
        z-index: 3;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @media only screen and (max-width: $bp-xxl) {
            margin-top: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-top: 0;
        }

    }

    &__title {
        font-size: 48px;
        line-height: 56px;
        letter-spacing: -0.02em;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 42px;
            line-height: 52px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 34px;
            line-height: 42px;
            margin-bottom: 8px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 4px;
            font-size: 24px;
            line-height: 32px;
        }
    }

    &__subtitle{
        display: inline-flex;
        font-weight: 600;
        font-size: 20px;
        line-height: 32px;
        margin-bottom: 32px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
            line-height: 22px;
        }
    }

    &__text {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 5;
        font-size: 20px;
        line-height: 32px;
        color: $blue-dark;
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 18px;
            line-height: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            -webkit-line-clamp: 4;
            font-size: 16px;
            line-height: 22px;
            color: #fff;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
            -webkit-line-clamp: 3;
        }
    }

    &__button{
        margin-top: auto;
        min-width: 250px;
        height: 52px;
        background-color: rgba(249, 250, 251, 0.2);
        border-color: transparent;
        color: $light;

        @media only screen and (min-width: $bp-lg + 1) {
            background-color: $blue;
            color: $light;
            fill: $light;

            &:hover{
                background-color: $blue-dark;
            }
        
            &:active{
                background-color: $blue-light;
            }
        }

        @media only screen and (max-width: $bp-lg) {
            min-width: auto;
            width: 100%;
        }
    }


    &--blue{
        color: #fff;
        fill: #fff;
        background-color: $blue;

        &:hover{
            background-color: $blue-dark;
        }
    }

    &--blue &__header>*::before{
        background-color: #4B5563;

        @media only screen and (max-width: $bp-lg) {
            background-color: #D1D5DB;
        }
    }

    &--blue &__button{
        @media only screen and (min-width: $bp-lg + 1) {
            background-color: $light;
            color: $dark-light;
            fill: $dark-light;
            border: 1px solid #E5E7EB;
    
            &:hover{
                background-color: $light-light;
            }
    
            &:active{
                background-color: #E5E7EB;
                border-color: #D1D5DB;
            }
        }
    }
    
    &--blue &__text{
        color: #fff;
    }



    &--md{
        min-height: 600px;
        color: #fff;
        fill: #fff;
        background-color: $blue;

        &::after{
            content: "";
        }

        @media only screen and (max-width: $bp-md) {
            min-height: 400px;
        }
    }

    &--md &__inner{
        padding: 16px;
    }

    &--md:hover &__img{
        clip-path: none;

        img{
            transform: scale(104%);
        }
    }

    &--md:hover &__button{
        background-color: $light;
        border-color: $light;
        color: $dark;
    }

    &--md &__img-block{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        margin: 0;
    }

    &--md &__img{
        max-height: none;
    }

    &--md &__header{
        justify-content: flex-end;
        flex-wrap: wrap;

        &>*{
            &::before{
                content: none;
            }
        }
    }

    &--md &__date{
        margin-left: auto;
    }


    &--md .like{

        &::before{
            content: "";

            @media only screen and (max-width: $bp-lg) {
                content: "";
            }
        }

        fill: rgba(236, 125, 66, 0);
        stroke: #fff;
        
        &:hover{
            fill: rgba(236, 125, 66, 0.5);
        }
    
        &:active{
            fill: rgb(236, 125, 66);
            stroke: rgb(236, 125, 66);
            opacity: 0.8;
        }
    
        &.is-favorite{
            fill: #EC7D42;
            stroke: #EC7D42;
    
            &:hover{
                fill: #BA531A;
                stroke: #BA531A;
            }
        
            &:active{
                fill: #A23F02;
                stroke: #A23F02;
                opacity: 1;
            }
        }
    }

    &--md &__logo{
        display: none;
    }
    
    &--md &__img{
        width: 100%;
        height: 100%;
        clip-path: none !important;
    }

    &--md &__main{
        width: 100%;
    }

    &--md &__title{
        font-size: 34px;
        line-height: 42px;
        margin-bottom: 4px;

        @media only screen and (max-width: $bp-md) {
            font-size: 24px;
            line-height: 32px;
        }
    }

    &--md &__subtitle{
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
            line-height: 22px;
        }
    }

    &--md &__text{
        -webkit-line-clamp: 4;
        font-size: 14px;
        line-height: 22px;
        color: #fff;

        @media only screen and (max-width: $bp-md) {
            -webkit-line-clamp: 3;
        }
    }
    
    &--md &__button{
        background-color: rgba(249, 250, 251, 0.2);
        border-color: transparent;
        color: $light;
        width: 100%;
    }



    &--long{
        min-height: 234px;
        color: #fff;
        fill: #fff;
        background-color: $blue;

        @media only screen and (max-width: $bp-md) {
            min-height: 400px;
        }

        &::after{
            content: "";
        }
    }

    &--long &__inner{
        padding: 16px;
    }

    &--long:hover &__img{
        clip-path: none;

        img{
            transform: scale(104%);
        }
    }

    &--long:hover &__button{
        background-color: $light;
        border-color: $light;
        color: $dark;
    }

    &--long &__img-block{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        margin: 0;
    }

    &--long &__img{
        max-height: none;
    }

    &--long &__header{
        justify-content: flex-end;
        flex-wrap: wrap;
        margin-bottom: 4px;

        &>*{
            &::before{
                content: none;
            }
        }
    }

    &--long &__date{
        margin-left: auto;
    }


    &--long .like{

        &::before{
            content: "";

            @media only screen and (max-width: $bp-lg) {
                content: "";
            }
        }

        fill: rgba(236, 125, 66, 0);
        stroke: #fff;
        
        &:hover{
            fill: rgba(236, 125, 66, 0.5);
        }
    
        &:active{
            fill: rgb(236, 125, 66);
            stroke: rgb(236, 125, 66);
            opacity: 0.8;
        }
    
        &.is-favorite{
            fill: #EC7D42;
            stroke: #EC7D42;
    
            &:hover{
                fill: #BA531A;
                stroke: #BA531A;
            }
        
            &:active{
                fill: #A23F02;
                stroke: #A23F02;
                opacity: 1;
            }
        }
    }

    &--long &__logo{
        display: none;
    }
    
    &--long &__img{
        width: 100%;
        height: 100%;
        clip-path: none !important;
    }

    &--long &__main{
        width: 100%;
    }

    &--long &__title{
        font-size: 20px;
        line-height: 28px;
        margin-bottom: 4px;
    }

    &--long &__subtitle{
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 8px;
    }

    &--long &__text{
        -webkit-line-clamp: 3;
        font-size: 14px;
        line-height: 22px;
        color: #fff;
        margin-bottom: 16px;
    }
    
    &--long &__button{
        background-color: rgba(249, 250, 251, 0.2);
        border-color: transparent;
        color: $light;
        font-size: 14px;
        min-width: 115px;

        @media only screen and (max-width: $bp-lg) {
            width: auto;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
        }
    }

    

    &--line{
        min-height: 140px;
        color: $dark;
        fill: $dark;
        background-color: #fff;
        padding-left: 200px;

        &::after{
            content: none;
        }

        
        @media only screen and (max-width: $bp-md) {
            min-height: 300px;
            padding-left: 0;
            flex-direction: column;
        }
    }

    &--line &__inner{
        padding: 16px 16px 16px 32px;

        @media only screen and (max-width: $bp-md) {
            padding: 16px;
        }
    }

    &--line:hover &__img{
        clip-path: none;

        img{
            transform: scale(104%);
        }
    }

    &--line:hover &__button{
        background-color: $light;
        border-color: $light;
        color: $dark;
    }

    &--line &__img-block{
        width: 200px;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        margin: 0;

        @media only screen and (max-width: $bp-md) {
            position: relative;
            width: 100%;
            height: 220px;
            order: -1;
        }
    }

    &--line &__img{
        max-height: none;
    }

    &--line &__header{
        justify-content: flex-end;
        flex-wrap: wrap;
        margin-bottom: 0;

        &>*{
            &::before{
                content: none;
            }
        }
    }

    &--line &__date{
        margin-bottom: 8px;
    }

    &--line .like{
        margin-bottom: 8px;
    }

    &--line &__labels{
        margin-bottom: 8px;
    }

    &--line &__label{
        font-size: 14px;
        line-height: 100%;
    }

    &--line &__date{
        margin-left: auto;
    }

    &--line .like{

        &::before{
            content: "";

            @media only screen and (max-width: $bp-lg) {
                content: "";
            }
        }

        @media only screen and (max-width: $bp-lg) {
            width: auto;
            height: auto;
            position: static;
            stroke: $blue;

            .icon{
                width: 24px;
                height: 24px;
            }

            &:active{
                stroke: $blue;
            }
        
            &.is-favorite{
                stroke: #EC7D42;
        
                &:hover{
                    stroke: #BA531A;
                }
            
                &:active{
                    stroke: #A23F02;
                }
            }
        }
    }

    &--line &__logo{
        display: none;
    }
    
    &--line &__img{
        width: 100%;
        height: 100%;
        clip-path: none !important;
    }

    &--line &__main{
        width: 100%;
    }

    &--line &__title{
        font-size: 20px;
        line-height: 28px;
        margin-bottom: 4px;
    }

    &--line &__subtitle{
        display: none;
    }

    &--line &__text{
        -webkit-line-clamp: 2;
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 0;

        @media only screen and (max-width: $bp-lg) {
            color: $dark;
        }

        @media only screen and (max-width: $bp-md) {
            -webkit-line-clamp: 4;
        }
    }
    
    &--line &__button{
        background-color: rgba(249, 250, 251, 0.2);
        border-color: transparent;
        color: $light;
        font-size: 14px;
        min-width: 115px;
        display: none;

        @media only screen and (max-width: $bp-lg) {
            width: auto;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
        }
    }
}
