@import "@theme/vars.scss";

.link{
    display: inline-flex;
    align-items: center;
    color: $blue;
    fill: $blue;
    text-decoration: none;
    transition: fill 0.35s ease, color 0.35s ease, opacity 0.35s ease;

    &:hover{
        color: $blue-medium;
        fill: $blue-medium;
    }

    span{

    }

    .icon + span {
        margin-left: 5px;
    }

    span + .icon{
        margin-left: 5px;
    }

    &-white{
        color: #fff;
        fill: #fff;

        &:hover{
            color: $violet;
            fill: $violet;
        }
    }
}