@import "@theme/vars.scss";

.generator {
    display: flex;
    justify-content: space-between;
    padding: 32px 16px;

    @media only screen and (max-width: $bp-lg) {
        padding: 16px;
    }

    @media only screen and (max-width: $bp-md) {
        flex-direction: column;
    }

    &::before{
        content: "";
        display: block;
        margin: 0 16px;
        width: 1px;
        flex-shrink: 0;
        background-color: #D1D5DB;
        order: 2;

        @media only screen and (max-width: $bp-md) {
            margin: 16px 0;
            width: 100%;
            height: 1px;
        }
    }

    &__form {
        order: 1;
        width: 386px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xl) {
            width: 300px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
        }
    }

    &__item {
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 16px;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 14px;
        line-height: 22px;
        color: #4B5563;
        margin-bottom: 16px;
    }

    &__result &__title{
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }
    }

    &__list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin: 0 -4px -8px;

        &>*{
            margin: 0 4px 8px;
        }
    }

    &__result {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        order: 3;
        padding-left: 16px;
        flex-grow: 1;

        @media only screen and (max-width: $bp-md) {
            padding: 0;
        }
    }

    &__preview {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-bottom: 32px;
        border: 1px solid #D1D5DB;

        // @media only screen and (max-width: $bp-xl) {
        //     min-height: 292px;
        // }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 16px;
        }

        img{
            height: 282px;
            max-width: 100%;
            object-fit: contain;

            @media only screen and (max-width: $bp-xl) {
                height: 240px;
            }

            @media only screen and (max-width: $bp-lg) {
                height: 195px;
            }
        }
    }

    &__preview-item{
        display: none;
        width: 100%;

        &.is-active{
            display: flex;
            animation: fadeIn 0.5s ease forwards;
        }

        svg{
            width: 100%;

            path, rect {
                transition: fill 0.35s ease;
            }
        }
    }

    &__button {
        margin-left: auto;
        min-width: 200px;
        height: 44px;
        padding: 0 16px;
        font-size: 16px;    

        .button__icon{
            width: 24px;
            height: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            min-width: auto;
        }
    }
}
