@import "@theme/vars.scss";

.footer {
    padding: 112px 0;
    background-color: $blue;

    @media only screen and (max-width: $bp-lg) {
        padding: 45px 0;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 24px 0;
    }

    &__container {
    }

    &__inner {
        position: relative;
        display: flex;
        justify-content: space-between;
        
        @media only screen and (max-width: $bp-xl) {
            flex-direction: column;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 32px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 24px;
        }
    }

    &__logo {
        display: flex;
        width: 320px;
        height: 80px;
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-xxl) {
            width: 280px;
            height: 70px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 180px;
            height: 45px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 0;
        }

        @media only screen and (max-width: $bp-md) {
            width: 114px;
            height: 28px;

        }

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__socials {
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__social {
        width: 32px;
        height: 32px;
        flex-shrink: 0;
        margin-right: 16px;
        fill: $light-light;
        transition: fill 0.35s ease;

        .icon{
            width: 100%;
            height: 100%;
        }

        &:hover{
            fill: $violet-dark;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }

    &__rights {
        display: block;
        color: rgba(250, 250, 250, 0.5);
        position: absolute;
        bottom: 0;
        left: 0;
        max-width: 480px;

        @media only screen and (max-width: $bp-xl) {
            position: static;
            max-width: none;
            width: 100%;
        }

    }

    &__rights-main{
        display: flex;
        flex-direction: column;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
        }
    }

    &__rights-links{
        display: flex;
        flex-direction: column;
        align-items: center;
        align-items: flex-start;
        flex-wrap: wrap;
        margin-top: 16px;
        font-size: 12px;
        line-height: 32px;

        @media only screen and (max-width: $bp-lg) {
            line-height: 24px;
        }

        a{
            text-transform: uppercase;
            color: rgba(250, 250, 250, 0.5);

            &:last-of-type{
                margin-right: 0;
            }
        }
    }

    &__nav {
        box-sizing: border-box;
        display: flex;
        margin-left: 64px;

        @media only screen and (max-width: $bp-xl) {
            margin-left: 0;
            width: 100%;
            margin-bottom: 32px;
        }

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            margin-bottom: 0;
        }
    }

    &__group {
        display: flex;
        flex-direction: column;
        margin-right: 32px;
        flex-grow: 1;
        width: 220px;

        @media only screen and (max-width: $bp-xxl) {
            width: 200px;
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: auto;
            flex-grow: 1;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            padding-bottom: 24px;
            border-bottom: 1px solid $blue-light;
            margin-bottom: 24px;
        }

        &:last-of-type{
            width: auto;
            margin-right: 0;
        }
    }
    
    &__group-list{
        display: flex;
        flex-direction: column;
    }

    &__nav-item {
        display: inline-flex;
        color: $light;
        font-size: 16px;
        line-height: 135%;
        opacity: 0.8;
        margin-bottom: 16px;
        transition: color 0.35s ease, opacity 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            padding: 8px;
            margin-bottom: 0;
            font-size: 14px;
        }

        &:hover{
            opacity: 1;
            color: $violet-dark;
        }

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__nav-title {
        margin-bottom: 20px;
        color: $light;

        @media only screen and (max-width: $bp-lg) {
            padding: 8px;
            margin-bottom: 0;
        }
    }
}
