@import "@theme/vars.scss";
.cooperation {
    &__menu {
        margin-bottom: 48px;
        overflow-x: auto;
        max-width: 100%;
        position: sticky;
        top: 168px;
        background-color: #fff;
        z-index: 10;

        @media only screen and (max-width: $bp-lg) {
            top: 70px;
        }
    }

    &__menu-list {
        position: relative;
        display: flex;
        flex-shrink: 0;
        min-width: 100%;
        width: max-content;
        
        &::after{
            content: "";
            position: absolute;
            display: block;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: #E5E7EB;
            z-index: 2;
        }
    }

    &__menu-item {
        position: relative;
        box-sizing: border-box;
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        white-space: nowrap;
        height: 40px;
        padding: 0 12px;
        text-decoration: none;
        font-size: 16px;
        line-height: 28px;
        font-weight: 600;
        color: $dark;
        
        &::after{
            content: "";
            display: block;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: $blue;
            z-index: 3;
            opacity: 0;
            transition: opacity 0.35s ease;
        }
    
        &:hover::after{
            opacity: 1;
        }
    }

    &__block {
        margin-bottom: 64px;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__header {
        margin-bottom: 16px;
    }

    &__title {
        color: $blue;
    }

    &__content {
    }

    &__text {
        color: $dark-light;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__partners {
    }

    &__partners-title {
        margin-bottom: 16px;
        color: $blue;
    }

    &__partners-list{
        display: flex;
        flex-wrap: wrap;
        margin: 0 -17px -4px;

        @media only screen and (max-width: $bp-xl) {
            margin: 0 -12px -4px;
        }
    }

    &__partner {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        width: calc(25% - 34px);
        padding: 8px 8px 34px;
        margin: 0 17px 4px;
        text-decoration: none;
        color: $gray-blue;
        opacity: 0.5;
        transition: opacity 0.35s ease;

        @media only screen and (max-width: $bp-xl) {
            margin: 0 12px 4px;
            width: calc(33.33% - 24px);
            padding-bottom: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: calc(50% - 24px);
            padding: 0 8px 16px;
        }

        @media only screen and (max-width: $bp-md) {
            width: calc(100% - 24px);
        }

        &:hover{
            opacity: 1;
        }

        img{
            max-width: 100%;
            height: 56px;
            object-fit: contain;
        }

        span{
            font-size: 12px;
            line-height: 18px;
            margin-top: 16px;
            text-align: center;
        }
    }

    &__generator {
    }
}