@import "@theme/vars.scss";
@import "@theme/animations.scss";

.header {
    box-sizing: border-box;
    position: sticky;
    top: 0;
    right: 0;
    z-index: 10;
    width: 100%;

    &.is-fixed{
    }

    &.is-active &__content{
        visibility: visible;
        opacity: 1;
    }

    &__container {
    }

    &__main{
        background-color: $blue;
        position: relative;
        z-index: 3;

        @media only screen and (max-width: $bp-lg) {
            background-color: $light;
        }
    }

    &__main &__inner{
        min-height: 88px;
        
        @media only screen and (max-width: $bp-lg) {
            min-height: auto;
            justify-content: space-between;
        }
    }

    &__content{
        width: 100%;
        position: relative;
        z-index: 2;
        background-color: $light;

        @media only screen and (max-width: $bp-lg) {
            position: absolute;
            top: 100%;
            left: 0;
            background-color: $blue;
            visibility: hidden;
            opacity: 0;
            transition: opacity 0.5s ease, visibility 0.5s ease;
        }
    }

    &__content &__inner{
        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            padding: 24px 0;
        }
    }

    &__inner {
        box-sizing: border-box;
        padding: 15px 0;
        display: flex;
        align-items: center;
        position: relative;
    }

    &__logo {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        width: 196px;
        height: 48px;
        order: 1;
        margin-right: auto;
        transition: opacity 0.35s cubic-bezier(.77,0,.175,1) 0.25s;

        @media only screen and (max-width: $bp-lg) {
            order: 1;
            height: 28px;
            width: 114px;
            margin-right: 8px;

            &.is-hide{
                opacity: 0;
                transition-delay: 0s;
            }
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__burger { 
        box-sizing: border-box;
        cursor: pointer;
        position: relative;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        flex-shrink: 0;
        border: 1px solid #E5E7EB;
        background-color: $light;
        border-radius: 8px;
        order: 3;
        transition: background-color 0.35s ease, border-color 0.35s ease;
        display: none;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
            margin-left: 8px;
        }

        &::before, 
        &::after,
        span {
            content: "";
            width: 14px;
            height: 1.5px;
            border-radius: 2px;
            background-color: #101010;
            position: absolute;
            left: 50%;
            top: 50%;
            transform-origin: center;
            transition: transform 0.65s ease, opacity 0.5s ease, background-color 0.35s ease;
        }
        
        span{
            opacity: 1;
            transform: translate(-50%, -50%);
        }

        &::before{
            transform: translate(-50%, calc(-50% - 4px));
        }
        
        &::after{
            transform: translate(-50%, calc(-50% + 4px));
        }

        &:hover{ 
            border-color: $blue;
        }

        &.is-active{
            background-color: $blue;
            border-color: $blue;

            &:before{
                transform: translate(-50%, -50%) rotate(-45deg);
                background-color: #fff;
            }

            &:after{
                transform: translate(-50%, -50%) rotate(45deg);
                background-color: #fff;
            }

            span{
                transform: translate(-50%, -50%);
                opacity: 0;
            }
            
            &:hover{
                &::before,
                &::after,
                span{
                    background-color: $violet-medium;
                }
            }
        }
    }

    &__profile {
        box-sizing: border-box;
        cursor: pointer;
        display: flex;
        align-items: center;
        order: 2;

        color: #fff;
        text-decoration: none;
        margin-left: auto;
   
        @media only screen and (max-width: $bp-lg) {
            order: 1;
            margin-left: 0;
            width: 100%;
            margin-bottom: 16px;
        }

        .button{
            height: 44px;

            @media only screen and (max-width: $bp-lg) {
                width: 100%;
            }

            &__icon{
                display: none;
            }
        }
    }

    &__profile-link{
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 4px 8px;
        transition: background-color 0.35s ease;

        &:hover{
            background: rgba(221, 205, 255, 0.1);
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 0;
        }

        &>span{
            margin-left: 12px;
            max-width: 120px;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    &__profile-img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        flex-shrink: 0;
        border-radius: 50%;
        overflow: hidden;

        @media only screen and (max-width: $bp-lg) {
            width: 32px;
            height: 32px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__search {
        order: 2;
        margin: 0 32px;

        @media only screen and (max-width: $bp-xxl) {
            margin: 0 24px;
        }
        
        @media only screen and (max-width: $bp-lg) {
            position: absolute;
            right: 40px;
            top: 50%;
            transform: translateY(-50%);
            order: 2;
            width: 100%;
            width: calc(100% - 40px);
            max-width: none;
            margin: 0;
        }
    }

    &__blind {
        display: none;
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        fill: $light;
        color: $light;
        order: 3;
        margin-left: 64px;
        transition: color 0.35s ease, fill 0.35s ease;

        @media only screen and (max-width: $bp-xxl) {
            margin-left: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-left: 0;
            padding: 12px 0;
            width: 100%;
            position: static;
            order: 4;
            fill: #fff;
            color: #fff;
        }

        .icon{
            width: 32px;
            height: 32px;

            @media only screen and (max-width: $bp-lg) {
                width: 16px;
                height: 16px;
            }
        }

        span{
            display: none;
            font-weight: 600;
            font-size: 14px;
            line-height: 14px;
            margin-left: 6px;

            @media only screen and (max-width: $bp-lg) {
                display: flex;
            }
        }

        &:hover{
            color: $violet-dark;
            fill: $violet-dark;
        }
    }

    &__nav {

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            border-bottom: 1px solid $blue-light;
            padding-bottom: 16px;
            margin: 0 0 15px;
        }
        
        .nav {
            display: flex;
            align-items: center;
            width: auto;

            @media only screen and (max-width: $bp-lg) {
                flex-wrap: wrap;
            }

            &__item,
            &__trigger {
                box-sizing: border-box;
                cursor: pointer;
                position: relative;
                display: inline-flex;
                align-items: center;
                text-decoration: none;
                margin-right: 20px;
                padding: 10px 0;
                color: $dark;
                fill: $light-medium;
                font-weight: 600;
                font-size: 14px;
                line-height: 17px;
                transition: color 0.35s ease, fill 0.35s ease;


                @media only screen and (max-width: $bp-lg) {
                    width: calc(50% - 16px);
                    padding: 8px;
                    margin-right: 32px;
                    margin-left: 0;
                    
                    &:hover{
                        color: $violet;
                        fill: $violet;
                    }

                    &:nth-of-type(even) {
                        margin-right: 0;
                    }
                }

                &::after{
                    content: "";
                    display: block;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 2px;
                    background-color: $dark;
                    transform: scaleX(0);
                    transform-origin: right;
                    will-change: transform;
                    transition: transform 0.4s cubic-bezier(.77,0,.175,1);

                    @media only screen and (max-width: $bp-lg) {
                        content: none;
                    }
                }

                &:hover::after{
                    transform: scaleX(1);
                    transform-origin: left;
                }
                
                .icon{
                    width: 20px;
                    height: 20px;
                }

                span{
                    margin-top: 2px;

                    @media only screen and (max-width: $bp-lg) {
                        margin-top: 0;
                    }
                }

                span ~ .icon{
                    margin-left: 8px;
                }

                .icon ~ span {
                    margin-left: 8px;
                }
            }

            &__trigger {
                order: 2;
                padding-right: 0;
                display: none;

                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }

                &.is-active{

                    .icon{
                        &:nth-of-type(1){
                            display: none;
                        }
        
                        &:nth-of-type(2){
                            display: flex;
                        }
                    }
                }

                .icon{

                    &:nth-of-type(2){
                        display: none;
                    }
                }
                
                &.is-visible{
                    display: inline-flex;
                    
                    @media only screen and (max-width: $bp-lg) {
                        display: none;
                    }
                }
            }

            &__dropdown {
                display: flex;
                position: fixed;
                top: 80px;
                left: 0;
                right: 0;
                z-index: 2;
                width: 100%;
                min-height: 80px;
                background-color: $blue-dark;
                visibility: hidden;
                opacity: 0;
                z-index: 2;
                transition: visibility 0.5s cubic-bezier(.77,0,.175,1), opacity 0.5s cubic-bezier(.77,0,.175,1);
               
                @media only screen and (min-width: $bp-lg + 1) {
                    &.is-active{
                        display: flex;
                        opacity: 1;
                        visibility: visible;
                    }  
                }

                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }
            }

            &__dropdown-inner{
                box-sizing: border-box;
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                width: 100%;
                max-width: 1280px;
                padding: 15px 20px;
                margin: 0 auto;

                @media only screen and (max-width: $bp-lg) {
                    display: inherit;
                    padding: 0;
                }
            }
        }

        &--main {
            order: 3;
            margin-left: auto;

            @media only screen and (max-width: $bp-lg) {
                order: 2;
                padding-top: 16px;
                border-top: 1px solid $blue-light;
            }

            .nav{

                &__item{
                    white-space: nowrap;

                    @media only screen and (max-width: $bp-xl) {
                        &:hover span{
                            display: inline-flex;
                        }
                    }
                    
                    span{
                        @media only screen and (max-width: $bp-xl) {
                            display: none;
                        }

                        @media only screen and (max-width: $bp-lg) {
                            display: inline-flex;
                        }
                    }

                    @media only screen and (max-width: $bp-lg) {
                        color: $light;
                        fill: $light;
                        white-space: normal;
                    }
                }
            }
        }
        
        &--second {
            order: 1;
            flex-grow: 1;
            width: 100%;
            max-width: 880px;

            @media only screen and (max-width: $bp-xxl) {
                max-width: 70%;
            }

            @media only screen and (max-width: $bp-lg) {
                order: 3;
                margin-left: 0;
                margin-right: 0;
                max-width: none;

                &::after{
                    content: "";
                    display: block;
                    clear: both;
                }
            }

            .nav{
                flex-shrink: 0;
                width: max-content;

                @media only screen and (max-width: $bp-lg) {
                    flex-wrap: wrap;
                    width: 100%;
                }

                &__item{
                    color: $light;
                    fill: $light;
                    margin-right: 32px;

                    &::after{
                        background-color: #fff;
                    }
                }

                &__trigger{
                    color: $light;
                    fill: $light;

                    &::after{
                        background-color: #fff;
                    }
                }
            }
        }
    }
}