@import "@theme/vars.scss";

.select {
    display: flex;
    align-items: center;
    width: auto;
    min-width: 172px;
    flex-grow: 1;
    height: 52px;
    font-size: 16px;
    line-height: 28px;

    @media only screen and (max-width: $bp-lg) {
        height: 48px;
    }

    .icon{
        width: 24px;
        height: 24px;
        margin-right: 16px;
        fill: $orange;
    }
    

    .SumoSelect {
        width: 100%;
        height: 100%;

        &:focus > .CaptionCont, 
        &:hover > .CaptionCont, 
        &.open > .CaptionCont{
            box-shadow: none;
		    border-color: #9CA3AF;
        }

        &.open {
            .SelectBox{
                label {
                    rotate: -180deg;
                }
            }

            .optWrapper{
                animation: fadeInTop 0.5s ease forwards;
            }
        }

        &[is-selected="true"] .SelectBox{
            border-color: #9CA3AF;

        }

        .SumoUnder {
            display: none;
        }
    }

    .SelectBox {
        cursor: pointer;
        box-sizing: border-box;
        position: absolute;
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
        padding: 0 15px;
        border-radius: 0px;
        border: 1px solid #E5E7EB;
        background-color: $light;
        color: $dark;
        transition: background-color 0.35s ease 0.1s, border-color 0.35s ease, color 0.35s ease 0.1s;

        &:hover{
            border-color: #9CA3AF;
        }
        
        .placeholder{
            font-style: normal !important;
            color: $dark !important;
            transition: color 0.35s ease 0.1s;
        }


        label{
            margin: auto 16px auto 0;
            cursor: pointer;
            width: 24px !important;
            height: 24px !important;
            transition: rotate 0.35s ease;

            i{
                width: 100% !important;
                height: 100% !important;
                opacity: 1 !important;
                background-size: 100% !important;
                background-position: center !important;
                background-repeat: no-repeat;
                background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M20.4503 8.37967C20.7432 8.67257 20.7432 9.14744 20.4503 9.44033L13.9303 15.9603C12.8674 17.0232 11.1325 17.0232 10.0697 15.9603L3.54965 9.44033C3.25676 9.14744 3.25676 8.67257 3.54965 8.37967C3.84255 8.08678 4.31742 8.08678 4.61031 8.37967L11.1303 14.8997C11.6074 15.3768 12.3925 15.3768 12.8697 14.8997L19.3897 8.37967C19.6825 8.08678 20.1574 8.08678 20.4503 8.37967Z' fill='%236B7280'/%3E%3C/svg%3E") !important;
                transition: background-image 0.35s ease 0.1s;
            }
        }
    }

    .optWrapper {
        top: calc(100% + 15px) !important;
        z-index: 5 !important;
        width: 100%;
        padding: 0px;
        border-radius: 0px;
        background-color: $light;
        border-color: #D1D5DB;
        box-shadow: 0px 20px 25px -5px rgba(16, 24, 40, 0.1), 0px 8px 10px -6px rgba(16, 24, 40, 0.1);
        transform: translateY(0);
        transition: opacity 0.5s ease, visibility 0.5s ease, transform 0.5s ease;

        &.multiple .opt{
            padding-left: 16px;
        }
    }

    .options {
        box-sizing: border-box;
        max-height: 220px !important;
        overflow: hidden auto !important;

        @media only screen and (max-width: $bp-lg) {
            // max-height: 284px !important;
        }
    }

    .opt {
        cursor: pointer;
        box-sizing: border-box;
        display: flex;
        align-items: flex-start;
        height: 44px;
        padding: 8px 16px !important;
        border: none !important;
        color: $dark;
        transition: background-color 0.25s ease, color 0.25s ease;

        @media only screen and (max-width: $bp-lg) {
            height: 40px;
            padding: 6px 16px !important;
        }

        &.disabled{
            display: none !important;
        }

        span{
            position: relative !important;
            flex-shrink: 0;
            width: 20px !important;
            height: 20px !important;
            margin: 4px 10px 4px 0 !important;
            background-color: #fff;
            border-radius: 0px;
            border: 1px solid #AEBDCB !important;
            transition: border-color 0.35s ease, background-color 0.35s ease;

            &::after{
                content: "";
                display: block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100% !important;
                height: 100% !important;
                background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.10497 12.6041L7.08735 12.6217L2.6875 8.22185L4.12018 6.78917L7.10502 9.77402L11.879 5L13.3117 6.43268L7.12264 12.6218L7.10497 12.6041Z' fill='%23ffffff'/%3E%3C/svg%3E%0A") !important;
                background-size: 100%;
                background-position: center;
                background-repeat: no-repeat;
                opacity: 0;
                transform: translate(-50%, -50%) scale(0.4);
                transition: opacity 0.35s ease, transform 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.1s;

            }

            i{
                display: none !important;
            }
        }

        label{
            margin: auto 0;
        }

        &:hover,
        &.selected {
            background-color: #9CA3AF !important;
            color: $light;
        }

        &.selected{
            span{
                border-color: $blue !important;
                background-color: $blue !important;

                &::after{
                    content: "";
                    transform: translate(-50%, -50%) scale(1);
                    opacity: 1;
                }
            }
        }
    }

    &--md {
        height: 40px;
    }

    &--md .SelectBox{
        border-width: 0.5px;
    }

    &--round{
        height: 40px;
        font-size: 14px;
        line-height: 22px;
    }

    &--round .SumoSelect{
        &.open{
            .SelectBox{
                background-color: $blue;
                color: #fff;

                .placeholder{
                    color: #fff !important;
                }

                label i{
                    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M20.4503 8.37967C20.7432 8.67257 20.7432 9.14744 20.4503 9.44033L13.9303 15.9603C12.8674 17.0232 11.1325 17.0232 10.0697 15.9603L3.54965 9.44033C3.25676 9.14744 3.25676 8.67257 3.54965 8.37967C3.84255 8.08678 4.31742 8.08678 4.61031 8.37967L11.1303 14.8997C11.6074 15.3768 12.3925 15.3768 12.8697 14.8997L19.3897 8.37967C19.6825 8.08678 20.1574 8.08678 20.4503 8.37967Z' fill='%23ffffff'/%3E%3C/svg%3E") !important;
                }
            }
        }
    }

    &--round .SelectBox{
        border-radius: 50px;
        border: 0;
        font-weight: 600;
        background-color: #F3F4F6;

        label{
            width: 16px !important;
            height: 16px !important;
        }
    }

    &--round .optWrapper{
        top: calc(100% + 6px) !important;
        border-radius: 4px;
    }

    &--round .options{
        max-height: 114px !important;
    }

    &--round .opt{
        height: 38px;

        span{
            margin: 0px 6px 0px 0 !important;
        }
    }


    &--round .icon{
        width: 20px;
        height: 20px;
        margin-right: 12px;
    }
}