@import "@theme/vars.scss";

.registration {
    flex-grow: 1;
    padding: 0 !important;
    display: flex;
    justify-content: center;
    color: $dark;
    background-color: $light;

    &__inner {
        box-sizing: border-box;
        display: flex;
        flex-shrink: 0;
        flex-grow: 1;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;
        min-width: 40%;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-lg) {
            min-width: auto;
            align-items: center;
            width: 100%;
        }
    }

    &__main {
        display: flex;
        flex-direction: column;
        max-width: 100%;
        box-sizing: border-box;
        padding: 40px 115px;
        width: 720px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            max-width: 660px;
            padding: 32px 80px 0;
            flex-grow: 1;
            justify-content: center;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 24px 20px 0;
            max-width: 500px;
        }
        
    }

    &__header {
        display: flex;
        flex-direction: column;
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg) {
            align-items: center;
            margin-bottom: 0;
        }
    }

    &__logo {
        display: flex;
        width: 196px;
        height: 48px;
        
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__body {
        position: relative;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        padding: 24px 0;

        @media only screen and (max-width: $bp-lg) {
            padding: 32px 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin: auto 0;
            text-align: center;
        }
    }

    &__back{
        margin-bottom: 16px;
        font-weight: 600;
        font-size: 14px;
        line-height: 100%;
        height: 32px;

        @media only screen and (max-width: $bp-lg) {
            position: absolute;
            top: 36px;
            height: auto;
            left: 0;
        }

        .icon{
            width: 16px;
            height: 16px;

            @media only screen and (max-width: $bp-lg) {
                width: 24px;
                height: 24px;
            }
        }

        span{
            @media only screen and (max-width: $bp-lg) {
                display: none;
            }
        }
    }

    &__back + &__title{
        @media only screen and (max-width: $bp-lg) {
            padding:0  24px;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.02em;

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
        }
    }

    &__subtitle {
        margin-top: 8px;

        @media only screen and (max-width: $bp-lg) {
            padding: 0 18px;
        }
    }

    &__image {
        width: 250px;
        height: 250px;

        @media only screen and (max-width: $bp-lg) {
            width: 200px;
            height: 200px;
            margin-bottom: 24px;
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        width: 100%;
        margin-top: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 24px;
        }

        &>form{
            display: flex;
            flex-direction: column;
            flex-grow: 1;
        }
    }

    &__form-block {
        display: block;
        // display: flex;
        // flex-direction: column;
        // align-items: flex-start;
        width: 100%;
        margin-bottom: 20px;

        // @media only screen and (max-width: $bp-lg) {
        //     align-items: center;
        // }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__form-block + &__form-block{
        border-top: 1px solid rgba(76, 78, 100, 0.12);
        padding-top: 20px;
    }

    &__form-title {
        font-weight: 600;
        font-size: 20px;
        line-height: 135%;
        letter-spacing: -0.02em;
        margin: 16px 0;
    
        @media only screen and (max-width: $bp-lg) {
            font-size: 18px;
        }

        &:first-child{
            margin-top: 0;
        }
    }

    &__form-title + &__form-text{
        margin-top: -16px;
    }

    &__form-text{
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 16px;
    }
    
    &__checklist {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-bottom: 16px;
    }

    &__checklist-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        color: #D1D5DB;
        fill: transparent;
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 8px;
        transition: color 0.35s ease, fill 0.35s ease;

        &:last-of-type{
            margin-bottom: 0;
        }

        .icon{
            width: 20px;
            height: 20px;
            margin-left: 16px;
        }

        &.is-active{
            color: #16A34A;
            fill: #16A34A;
        }
    }

    &__input,
    &__select,
    &__checkbox {
        width: 100%;
        margin-bottom: 16px;
        max-width: none;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__checkbox{
        margin-top: 30px;
    }

    &__badges{
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 12px;
        }

        &--grow{
            .badge{
                height: 44px;
                padding: 0 16px;
            }
        }
    }

    &__subtitle + &__buttons{
        margin-top: 16px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 12px;
        }
    }

    &__buttons{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin: auto -8px 0;
        width: calc(100% + 16px);
    }

    &__button {
        flex-grow: 1;
        margin: 16px 8px 0;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 8px;
            height: 40px;
        }
    }


    &__form-link {
        display: inline-flex;
        align-items: center;
        text-decoration: none;
        color: $dark-light;
        opacity: 0.5;
        font-weight: 600;
        font-size: 16px;
        line-height: 100%;
        transition: fill 0.35s ease, color 0.35s ease, opacity 0.35s ease;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 12px;
        }

        &:hover{
            opacity: 1;
        }

        &:is(button){
            color: $blue;
            fill: $blue;
            opacity: 1;
    
            &:hover,
            &:active {
                color: $blue-medium;
                fill: $blue-medium;
            }
        }
        
        .icon + span {
            margin-left: 5px;
        }
    
        span + .icon{
            margin-left: 5px;
        }
    }


    &__login {
    }

    &__login-title {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 22px 0 24px;
        font-size: 16px;
        line-height: 28px;
        color: rgba(0, 0, 0, 0.85);

        @media only screen and (max-width: $bp-lg) {
            margin: 16px 0;
        }

        &::after,
        &::before {
            content: "";
            display: inline-flex;
            background-color: #000;
            opacity: 0.06;
            height: 1px;
            flex-grow: 1;
        }

        span{
            padding: 0 16px;
        }
    }

    &__login-list {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__login-link {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        margin: 0 8px;
        background-color: $light;
        border: 1px solid #E5E7EB;
        fill: #D1D5DB;
        transition: border-color 0.35s ease, fill 0.35s ease;

        &:hover{
            fill: $blue;
            border-color: $blue;
        }

        .icon{
            width: 16px;
            height: 16px;
        }
    }


    &__other {
        display: flex;
        flex-direction: column;
        margin-top: 24px;
        font-size: 14px;
        line-height: 22px;
        font-weight: 600;
        color: #6B7280;

        @media only screen and (max-width: $bp-lg) {
            align-items: center;
            text-align: center;
            margin-top: 16px;
            font-size: 16px;
            line-height: 28px;
        }
    }

    &__bg {
        display: flex;
        height: auto;
        min-height: 100%;
        width: 60%;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: left center;
        }
    }

    &--artist &__main{
        flex-grow: 1;
    }

    &--artist &__title{
        font-weight: 600;
        font-size: 20px;
        line-height: 135%;
    }

    &--artist &__badges{
        .badge{
            font-size: 16px;
            line-height: 135%;
            height: 44px;

            @media only screen and (max-width: $bp-lg) {
                flex-grow: 1;
                white-space: normal;
                flex-shrink: 1;
            }

            &__wrapper{
                padding: 0 16px;
                
                .icon{
                    width: 24px;
                    height: 24px;
                }
            }
        }
    }
}