@import '@theme/vars.scss';

@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";
@import "~bootstrap/scss/modal";
@import "~bootstrap/scss/transitions";

.fade{
	transition: opacity 0.25s linear;
}
.modal {
	&.fade &-dialog{
        transform: none !important;
	}

	&.fade &-content{
		transform: translateY(15px);
        opacity: 0;
		transition: transform 0.25s ease-out, opacity 0.25s ease;

	}

	&.show &-content{
        opacity: 1;
		transform: none;
	}

	&-backdrop{
        background: rgba(0, 0, 0, 0.3);
        opacity: 0;
        backdrop-filter: blur(3px);

		&.show{
			opacity: 1;
		}
	}

	&--xl &-dialog{
        max-width: 1060px;
	}

	&--lg &-dialog{
        max-width: 700px;
	}
	
	&--md &-dialog{
        max-width: 614px;
	}
	

	&-dialog {
		border-radius: 0;
		border: none;
		max-width: 448px;
		margin-right: auto;
		margin-left: auto;

	}

	&-content {
		box-sizing: border-box;
		display: flex;
		background-color: #fff;
        color: $dark;
        padding: 24px 24px 32px;
        border-radius: 0;
        border: 1px solid #E5E7EB;

        @media only screen and (max-width: $bp-lg) {
            padding: 24px 16px 32px;
        }
	}

	&-close {
		width: 16px;
		height: 16px;
		background-color: transparent;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none;
		cursor: pointer;
        margin-left: auto;
        margin-bottom: 4px;

		.icon {
			width: 100%;
			height: 100%;
			transition: fill 0.35s ease;
			fill: $blue-dark;
		}

		&:hover {
			.icon{
				fill: $blue-light;
			}
		}
	}

	&__main{
        display: block;
	}

        
	&__title{
        margin-bottom: 16px;
        
        &:last-child{
            margin-bottom: 0;
        }
    }

    &__text{
        margin-bottom: 16px;
    }

    &__repost-copy {
        width: 100%;
        margin-bottom: 16px;
        color: #6B7280;
        fill: #6B7280;
        justify-content: space-between;

        &:hover{
            fill: $dark;
            color: $dark;
        }

        .button__text{
            max-width: 100%;
            overflow: hidden;   
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }

        &.is-active{
            border-color: $blue;
            color: $blue;
            fill: $blue;
            transition-duration: 0.75s;
        }
    }

    &__repost-copy + &__text{
        padding-top: 16px;
        border-top: 1px solid #E5E7EB;
    }

    &__repost-text {
    }

    &__socials {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    &__socials-link {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 48px;
        height: 48px;
        flex-shrink: 0;
        margin-right: 8px;
        fill: $blue;
        transition: fill 0.35s ease;

        .icon{
            width: 100%;
            height: 100%;
        }

        &:hover{
            fill: $violet-dark;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }

    &__button-close {
        display: flex;
        margin-top: 32px;
        width: 100%;
    }

    &__schelude {
    }

    &__schelude-item {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        font-size: 16px;
        line-height: 28px;
        margin-bottom: 12px;
        padding: 0 8px;
        color: $dark;
        transition: background-color 0.35s ease;

        &:hover{
            background-color: $light;
        }

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__text {
    }

    &__organizers {
        margin-top: 32px;
        display: flex;
        flex-wrap: wrap;
        margin: 0 -8px -16px;
    }

    &__organizers-item {
        box-sizing: border-box;
        width: 50%;
        display: block;
        padding: 0 8px;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            margin-bottom: 8px;
        }
    }

    &__organizers-title {
        font-weight: 400;
        font-size: 16px;
        line-height: 100%;
        margin-bottom: 8px;
        color: #6B7280;
    }

    &__organizers-profile {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        padding: 8px;
        color: $dark;
        text-decoration: none;
        transition: background-color 0.35s ease;

        &:hover{
            background-color: #E5E7EB;
        }
    }

    &__organizers-img {
        display: flex;
        width: 32px;
        height: 32px;
        flex-shrink: 0;
        margin-right: 12px;
        border-radius: 50%;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__organizers-name {
        font-size: 16px;
        line-height: 100%;
        font-weight: 600;

    }

    &__form {

        &--grid{
            form{
                display: flex;

                @media only screen and (max-width: $bp-lg) {
                    flex-direction: column;
                    align-items: center;
                }
            }
        }
    }

    &__form-grid{
        display: flex;
        flex-wrap: wrap;
        margin: 0 -16px;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin: 0;
        }

        &>*{
            flex: 1 0 50%;
            padding: 0 16px;

            @media only screen and (max-width: $bp-lg) {
                width: 100%;
                flex: initial;
                padding: 0;
            }
        }
    }

    &__form-grid &__input{
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 24px;

            .input{
                max-width: none;
            }
        }
        
        @media only screen and (max-width: $bp-md) {
            margin-bottom: 16px;
        }
    }

    &__input-img{
        width: 148px;
        height: 148px;
        flex-shrink: 0;
        margin-right: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 32px;
            margin-right: 0;
        }

        .input-img{
            width: 100%;
            height: 100%;
            border-radius: 0;
            border: 4px solid #fff;
            box-shadow: 0px 25px 50px -12px rgba(16, 24, 40, 0.1);

            &__img{

            }

            &__icon{
                opacity: 1;
                top: auto;
                bottom: 16px;
            }
        }
    }

    &__input{
        box-sizing: border-box;
        margin-bottom: 16px;

        &:last-of-type{
            margin-bottom: 32px;
        }

        &--file{
            margin-top: 32px;
        }
    }

    &__textarea{
        box-sizing: border-box;
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 16px;
        }

        .input{
            &__field{
                height: 204px;

                @media only screen and (max-width: $bp-lg) {
                    height: 150px;
                }
            }
        }
    }

    &__textarea + .checkbox{
        margin-top: -22px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 0;
        }
    }

    .checkbox{
        
    }

    &__input-label{
        font-size: 16px;
        line-height: 28px;
        color: #6B7280;
        margin-bottom: 4px;
    }

    &__form-buttons{
        display: flex;
        align-items: center;
        margin-top: 32px;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
        }
    }

    &__form-buttons &__button{
        height: 44px;
    }

    &__button{
        display: flex;
        width: 100%;
    }
}
