@import "@theme/vars.scss";

.checkbox {
    cursor: pointer;
    display: flex;

    &__input {
        display: none;
    }

    &__input:checked + &__wrapper &__icon-wrapper{
        border-color: transparent;
        background-color: $blue;
    }

    &__input:checked + &__wrapper &__icon{
        transform: scale(1);
        opacity: 1;
    }

    &:hover &__icon-wrapper{
        border-color: $blue;
    }

    &__wrapper {
        display: flex;
        align-items: flex-start;
    }

    &__icon-wrapper {
        box-sizing: content-box;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 16px;
        height: 16px;
        flex-shrink: 0;
        background-color: #fff;
        border: 1px solid #AEBDCB;
        transition: border-color 0.35s ease, background-color 0.35s ease;
    }

    &__icon {
        width: 100% !important;
        height: 100% !important;
        fill: #fff;
        opacity: 0;
        transform: scale(0.4);
        transition: opacity 0.35s ease, transform 0.25s cubic-bezier(.77,0,.175,1);
    }

    &__content {
        display: flex;
        margin: auto 0 auto 10px;

        span{
            display: inline;

            .tooltip-trigger{
                display: inline;
                margin-left: 5px;
            }
        }

        .icon + span{
            margin-left: 5px;
        }

        span + .icon{
            margin-left: 5px;
        }
    }



    &--md &__icon-wrapper{
        width: 18px;
        height: 18px;
        border-radius: 3px;
    }
}