@import "@theme/vars.scss";

.filter{
    display: flex;
    flex-grow: 1;
    
    @media only screen and (max-width: $bp-lg) {
        flex-wrap: wrap;
        justify-content: space-between;
    }

    form{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        flex-grow: 1;
        margin: 0 -4px -8px;
        max-width: calc(100% + 8px);

        @media only screen and (max-width: $bp-md) {
            flex-wrap: wrap;
        }

        & > *{
            margin: 0 4px 8px;

            &.filter__other{
                margin-right: -8px;
                margin-left: -8px;

                @media only screen and (max-width: $bp-md) {
                    margin-right: 4px;
                    margin-left: 4px;
                }
            }
        }
    }

    &__calendar + .badges{
        width: calc(100% - 290px);

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            padding-top: 8px;
            border-top: 1px solid #D1D5DB;
        }
    }

    &__search{
        width: 100%;
        flex-grow: 1;
        display: flex;
        align-items: center;
        flex-wrap: nowrap;

        .input{
            max-width: none;
            flex-grow: 1;
        }

        .button{
            min-width: 120px;
            height: 100%;
            margin-left: 16px;
            flex-shrink: 0;
        }
    }

    &__calendar{
        width: 260px;
        flex-shrink: 0;
        margin-left: 30px;
        order: 2;

        @media only screen and (max-width: $bp-md) {
            width: calc(100% - 8px);
            order: 0;
            max-width: none;
        }
    }

    &__datepicker,
    &__events {
        width: 100%;
        margin-bottom: 32px !important;
        max-width: calc(100% - 8px);

        &:last-child{
            margin-bottom: 8px !important;
        }
    }


    &__other{
        order: 3;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        flex-grow: 1;

        &>*{
            margin-right: 12px;
            margin-left: 12px;
        }

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: initial;


        }

        &:not(:first-child){
            &>*{
                margin-top: 8px;

                @media only screen and (max-width: $bp-md) {
                    margin-right: 0;
                    margin-left: 0;
                }
            }
            @media only screen and (max-width: $bp-md) {
                border-top: 1px solid #D1D5DB;
                padding-top: 8px;
                margin-top: 8px;
            }
        }
    }

    &__other &__selects{
        margin-right: 0;
        margin-left: 0;

        @media only screen and (max-width: $bp-md) {
            margin-right: -12px;
            margin-left: -12px;
        }
    }

    &__selects{
        display: flex;
        align-items: center;

        .select{
            margin: 0 12px;
        }
    }
}