@import "@theme/vars.scss";


.events{
    margin-bottom: 56px;

    @media only screen and (max-width: $bp-xl) {
        margin-bottom: 46px;
    }
    
    @media only screen and (max-width: $bp-lg) {
        margin-bottom: 32px;
    }

    &:last-child{
        margin-bottom: 0;
    }

    &__map{
        display: flex;
        width: 100%;
        height: 400px;

        @media only screen and (max-width: $bp-lg) {
            min-width: 100%;
            width: auto;
            margin: 0 -16px;
        }

        iframe{
            width: 100%;
            height: 100%;
            border: none !important;
            outline: none !important;
        }
    }

    &__map + *{
        @media only screen and (max-width: $bp-lg) {
            margin-top: 32px;
        }
    }

    &__map + .card-place{
        // margin-top: 32px;
    }

    .card-place{
        margin-bottom: 16px;

        &:last-child{
            margin-bottom: 0;
        }
    }
}