@import "@theme/vars.scss";

.comments {
    display: block;
    width: 100%;

    &__title {
        display: flex;
        align-items: center;
        color: $blue-dark;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 24px;
        }
        
        span {
            box-sizing: border-box;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            height: 32px;
            min-width: 32px;
            padding: 0 8px;
            border: 1.5px solid $blue;
            font-weight: 600;
            font-size: 14px;
            line-height: 14px;
            color: $blue;
            margin-left: 16px;

            @media only screen and (max-width: $bp-lg) {
                margin-left: 12px;
                height: 24px;
                padding: 0 4px;
                min-width: 24px;
            }

            @media only screen and (max-width: $bp-md) {
                margin-left: 8px;
            }
        }
    }

    &__body {
    }

    &__item {
        margin-bottom: 16px;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__footer {
        padding-top: 24px;
        margin-top: 24px;
        border-top: 1px solid #D1D5DB;

        &:first-child{
            border-top: 0;
            padding-top: 0;
            margin-top: 0;
        }
    }

    &__footer-title {
        margin-bottom: 24px;
        color: $blue-dark;

    }

    &__footer-subtitle{
        line-height: 28px;
        margin-bottom: 16px;
        
        @media only screen and (max-width: $bp-lg) {
            line-height: 20px;
        }
    }

    &__form {

        form{
            display: flex;
            align-items: center;
            flex-wrap: wrap;

            .input{
                width: 100%;
                margin-bottom: 16px;
            }
        }
    }

    &__rating{
        margin-left: auto;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            margin-left: 0;
        }
    }

    &__author {
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        padding: 4px;
        margin-bottom: 16px;
        color: #1F2937;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 8px;
        }

        span{
            
        }
    }

    &__author-img {
        display: flex;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        overflow: hidden;
        flex-shrink: 0;
        margin-right: 12px;

        @media only screen and (max-width: $bp-lg) {
            width: 24px;
            height: 24px;
            margin-right: 8px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.comment {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 12px;

    @media only screen and (max-width: $bp-lg) {
        padding: 8px 0;
    }

    &__header {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 8px;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 4px;
        }
    }

    &__header-main{
        display: flex;
        align-items: center;
   
    }

    &__author {
        display: flex;
        align-items: center;
        padding: 4px 0;
        margin-right: 16px;
        color: #1F2937;
        font-size: 16px;
        line-height: 100%;

        span{
            // max-width: 100%;
            // white-space: nowrap;
            // text-overflow: ellipsis;
            // overflow: hidden;
        }
    }

    &__img {
        display: flex;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        flex-shrink: 0;
        margin-right: 12px;
        overflow: hidden;

        @media only screen and (max-width: $bp-lg) {
            width: 24px;
            height: 24px;
            margin-right: 8px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__date {   
        color: $light-medium;
        font-size: 16px;
        line-height: 100%;
        flex-shrink: 0;
    }

    &__rating{
        display: inline-flex;
        align-items: center;
        font-family: 'Mont';
        color: $dark;
        margin-left: auto;
        padding-left: 16px;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            margin-left: 0;
            padding-left: 0;
            margin-top: 4px;
        }

        span{
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
        }

        .icon{
            width: 24px;
            height: 24px;
            flex-shrink: 0;
            margin-left: 8px;
            fill: #FAAD14;

            @media only screen and (max-width: $bp-lg) {
                width: 16px;
                height: 16px;
            }
        }

        strong{
            font-size: 18px;
            line-height: 24px;
            font-weight: 700;
            margin-left: 5px;

            @media only screen and (max-width: $bp-lg) {
                font-size: 16px;
            }
        }
    }

    &__body {
        
    }

    &__content{
        max-height: 120px;
        overflow: hidden;
        transition: max-height 0.5s cubic-bezier(.75,0.1,.15,1);

        @media only screen and (max-width: $bp-lg) {
            max-height: 100px;
        }
    }

    &__content-inner {
        color: $dark-light;
        font-family: 'Literata';
        font-size: 16px;
        line-height: 24px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 20px;
        }

        p{
            margin-bottom: 16px;

            &:last-of-type{
                margin-bottom: 0;
            }
        }
    }

    &__more {
        margin-top: 8px;
        font-weight: 600;
        font-size: 14px;
        line-height: 135%;
        text-decoration: underline;
        display: none;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 5px;
            font-size: 14px;
        }

        &.is-visible{
            display: inline-flex;
        }

        span{
            &:first-of-type{
                display: flex;
            }

            &:last-of-type{
                display: none;
            }
        }

        &.is-active{
            span{
                &:first-of-type{
                    display: none;
                }

                &:last-of-type{
                    display: flex;
                }
            }
        }
    }
}
