@import "@theme/vars.scss";


.card-author {
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 24px 16px;
    background-color: $light-light;
    text-decoration: none;
    color: $dark;
    text-align: center;
    overflow: hidden;
    transition: background-color 0.45s ease, color 0.45s ease;
    animation: fadeIn 0.45s ease forwards;

    &:hover{
        background-color: #E5E7EB;
    }

    &__img {
        display: flex;
        width: 112px;
        height: 112px;
        border-radius: 50%;
        overflow: hidden;
        flex-shrink: 0;
        box-shadow: 0px 25px 50px -12px rgba(16, 24, 40, 0.1);
        margin-bottom: 8px;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__nickname {
        font-weight: 600;
        font-size: 20px;
        line-height: 32px;
    }

    &__name {
        font-size: 16px;
        line-height: 28px;

    }

    &__info {
        width: 100%;
        margin-top: 8px;
        padding-top: 8px;
        border-top: 1px solid #D1D5DB;
        display: flex;
        flex-direction: column;
    }

    &__info-item {
        font-size: 16px;
        line-height: 28px;
        color: #6B7280;
    }
}
