@import "@theme/vars.scss";

.badges {

    &__list{

        display: flex;
        flex-wrap: wrap;
        margin: 0 -5px -10px;
        
        @media only screen and (max-width: $bp-lg) {
            margin: 0 -4px -8px;
        }

        &>*{
            margin: 0 5px 10px;

            @media only screen and (max-width: $bp-lg) {
                margin: 0 4px 8px;
            }
        }
    }

    &__slider{
        .slider{

            &__inner{
                overflow: visible;
            }
            &__slide{
                width: auto;
                
                &>*{
                    margin: 0 5px 10px;
        
                    @media only screen and (max-width: $bp-lg) {
                        margin: 0 4px 8px;
                    }
                }
            }
        }
    }

    .input{
        @media only screen and (max-width: $bp-md) {
            max-width: none;
            width: 100%;
        }
    }
}

.badge {
    cursor: pointer;
    box-sizing: border-box;
    display: inline-flex;
    align-items: center;
    display: flex;
    justify-content: center;
    flex-shrink: 0;
    height: 40px;
    text-decoration: none;
    font-weight: 600;
    font-size: 16px;
    line-height: 135%;
    white-space: nowrap;

    @media only screen and (max-width: $bp-lg) {
        height: 32px;
        font-size: 14px;
        line-height: 100%;
    }

    @media only screen and (max-width: $bp-md) {
        height: 24px;
    }

    &:hover &__wrapper{
        background-color: #E5E7EB;
    }

    &__input {
        display: none;
    }

    &__input[disabled] + &__wrapper{
        cursor: default;
    }

    &__wrapper {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        padding: 0 18px;
        text-decoration: none;
        color: $blue;
        fill: $blue;
        border-radius: 50px;
        background-color: $light-light;
        transition: background-color 0.35s ease, color 0.35s ease 0.075s, fill 0.35s ease 0.075s;

        @media only screen and (max-width: $bp-xl) {
            padding: 0 12px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 0 8px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 0 6px;
        }

        div.icon{
            mask-size: 100%;
            mask-repeat: no-repeat;
            background-color: $blue;
            transition: background-color 0.35s ease 0.075s;
        }

        .icon{
            @media only screen and (max-width: $bp-lg) {
                width: 20px;
                height: 20px;
            }

            @media only screen and (max-width: $bp-md) {
                width: 16px;
                height: 16px;
            }
        }

        span + .icon {
            margin-left: 12px;

            @media only screen and (max-width: $bp-lg) {
                margin-left: 8px;
            }

            @media only screen and (max-width: $bp-md) {
                margin-left: 6px;
            }
        }

        .icon + span {
            margin-left: 12px;

            @media only screen and (max-width: $bp-lg) {
                margin-left: 8px;
            }

            @media only screen and (max-width: $bp-md) {
                margin-left: 6px;
            }
        }
    }

    &--md{
        height: 32px;
        font-size: 14px;
        line-height: 100%;
    }

    &--md &__wrapper{
        padding: 0 16px;
        
        .icon{
            width: 16px;
            height: 16px;
        }
    }

    &--button{
        height: 44px;
    }

    &--button &__wrapper{
        border-radius: 0;
        padding: 0 16px;
        background-color: $light;
        color: $dark-light;
        fill: $dark-light;
        border: 1px solid #E5E7EB;

        &:hover{
            background-color: $light-light;
        }

        &:active{
            background-color: #E5E7EB;
            border-color: #D1D5DB;
        }
    }

    &__input:checked + &__wrapper,
    &.is-active &__wrapper {
        fill: $light;
        color: $light;
        background-color: $blue;

        div.icon{
            background-color: $light;
        }
    }
}
