@import "@theme/vars.scss";

.account {
    padding-right: 12px;
    padding-left: 12px;

    @media only screen and (max-width: $bp-lg) {
        padding-right: 0;
        padding-left: 0;
    }

    .container{
        @media only screen and (max-width: $bp-lg) {
            padding-right: 0;
            padding-left: 0;
        }
    }

    .section__inner{
        &>*{
            &:last-child{
                margin-bottom: 0;
            }

            @media only screen and (max-width: $bp-lg) {
                padding-right: 20px;
                padding-left: 20px;
            }

            @media only screen and (max-width: $bp-md) {
                padding-right: 16px;
                padding-left: 16px;
            }
        }
    }

    &__main {
        margin-bottom: 32px;
    }

    &__roles {
        margin-bottom: 24px;
    }

    &__title {
        color: $dark;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }
    }

    &__favorite {
        margin-bottom: 24px;
    }
    
    &__edit {
        margin-bottom: 24px;
    }

    &__actions {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-wrap: wrap;
        margin: 0 -8px -12px;

        
        @media only screen and (max-width: $bp-lg) {
            margin-bottom: -8px;
        }
    }

    &__action {
        height: 40px;
        margin: 0 8px 12px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 8px;
        }

        &--status{
            font-weight: 600;
            font-size: 20px;
            line-height: 32px;

            .icon + span{
                margin-left: 8px;
            }

            span + .icon{
                margin-left: 8px;
            }
        }
    }

    &__roles {
    }

    .account-section{
        margin-bottom: 48px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 32px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 24px;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }
}

.account-main {
    box-sizing: border-box;

    .account{
        &__actions {
            max-width: 50%;
            margin-left: -12px;
            margin-right: -12px;

            @media only screen and (max-width: $bp-md) {
                max-width: none;
                margin-right: -8px;
                margin-left: -8px;
                width: auto;
            }
        }
    
        &__action {
            height: 44px;
            margin-right: 12px;
            margin-left: 12px;

            @media only screen and (max-width: $bp-lg) {
                margin-left: 8px;
                margin-right: 8px;
            }

            &:not(.button--icon){

                @media only screen and (max-width: $bp-lg) {
                    flex-grow: 1;
                }
            }

            &--logout{
                @media only screen and (max-width: $bp-md) {
                    display: none;
                }
            }

            &--status{
                &:is(div){
                    display: none;

                    @media only screen and (max-width: $bp-md) {
                        display: inline-flex;
                    }
                }
                @media only screen and (max-width: 1099px) {
                    justify-content: flex-end;
                    width: 100%;
                }

                @media only screen and (max-width: $bp-lg) {
                    justify-content: center;
                }
            }
        }
    }

    &__bg {
        display: flex;
        height: 250px;
        min-width: 100%;
        margin: 0 -32px;

        @media only screen and (max-width: $bp-lg) {
            margin: 0 -20px;
        }

        @media only screen and (max-width: $bp-md) {
            margin: 0 -16px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: -16px;

        @media only screen and (max-width: $bp-md) {
            margin-top: -88px;
            flex-direction: column;
            align-items: initial;
        }
    }

    &__user {
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            margin-bottom: 16px;
        }
    }

    &__img-wrapper{
        display: flex;
        align-items: center;
        flex-shrink: 0;
        position: relative;
        margin-right: 12px;

        @media only screen and (max-width: $bp-md) {
            margin-right: 0;
            margin-bottom: 8px;
        }

        .account__action--logout{
            display: none;

            @media only screen and (max-width: $bp-md) {
                display: flex;
                width: 44px;
                height: 44px;
                position: absolute;
                bottom: 0;
                right: -16px;

                .button__icon{
                    width: 20px;
                    height: 20px;
                }
            }
        }
    }

    &__img {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        width: 156px;
        height: 156px;
        border-radius: 50%;
        flex-shrink: 0;
        border: 4px solid $light;
        box-shadow: 0px 25px 50px -12px rgba(16, 24, 40, 0.1);
        overflow: hidden;

        @media only screen and (max-width: $bp-lg) {
            width: 135px;
            height: 135px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 120px;
            height: 120px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__status{
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 44px;
        height: 44px;
        border-radius: 50%;
        border: 1px solid $light;
        fill: $light;
        background-color: $blue-light;
        position: absolute;
        bottom: 0;
        bottom: 2px;
        right: 10px;

        @media only screen and (max-width: $bp-md) {
            display: none;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
    }

    &__title {
        color: $dark-light;
        margin-bottom: 12px;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: 0 -12px -12px;

        @media only screen and (max-width: $bp-md) {
            justify-content: center;
        }
    }

    &__list-item {
        display: inline-flex;
        align-items: center;
        min-height: 32px;
        margin-left: 12px;
        margin-bottom: 12px;

        &::after{
            content: "";
            display: inline-flex;
            height: 24px;
            width: 1px;
            background-color: #E5E7EB;
            margin-left: 12px;
        }

        &:last-of-type{
            margin-right: 12px;
            
            &:after{
                content: none;
            }
        }

        strong{
            color: $dark-light;
            margin-right: 8px;
        }

        span{
            color: $light-medium;
        }
    }

}

.account-favorite {

    &__badges {
        margin-bottom: 16px;
        
        .badge{
            height: 40px;
            font-size: 16px;
            font-weight: 600;

            &__wrapper{
                padding: 0 18px;

                .icon{
                    width: 24px;
                    height: 24px;
                }

                &>span span{
                    color: #D1D5DB;
                }
            }
        }
    }

    &__tabs {
    }

    &__content {
        padding: 24px;
        background-color: $light;

        @media only screen and (max-width: $bp-lg) {
            padding: 20px;
            margin-right: -20px;
            margin-left: -20px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 16px;
        }

        .grid {

            &__block {
            }
        
            &__list {
                row-gap: 24px;
            }
        
            &__more {
                font-size: 16px;
            }
        }
    }
}

.account-edit {

    .account{
        &__actions{
            @media only screen and (max-width: $bp-md) {
                flex-grow: 1;
                min-width: 100%;
                margin-bottom: 12px;
                width: auto;
                order: -1;
            }
        }

        &__action{
            flex-grow: 1;

            @media only screen and (max-width: $bp-md) {
                height: 40px;
            }
        }

        &__title{
            margin-bottom: 0;
            @media only screen and (max-width: $bp-md) {
                font-size: 20px;
            }
        }
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: calc(100% + 64px);
        margin: 0 -32px;
        padding-bottom: 16px;
        border-bottom: 1px solid rgba(76, 78, 100, 0.12);

        @media only screen and (max-width: $bp-xxl) {
            width: 100%;
            margin: 0;
        }

        @media only screen and (max-width: $bp-md) {
            flex-wrap: wrap;
            padding-bottom: 8px;
        }

    }

    &__body{

    }

    &__block{
        padding: 32px 0;
        border-bottom: 1px solid rgba(76, 78, 100, 0.12);

        @media only screen and (max-width: $bp-lg) {
            padding: 24px 0;
        }
    }

    &__title {
        color: #000000;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
        }
    }

    &__subtitle{
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        color: $dark-light;
        margin-bottom: 4px;
    }

    &__text{
        font-size: 14px;
        line-height: 22px;
        color: $light-medium;
    }

    &__tumbler{
        margin-top: 16px;

        &:first-child{
            margin-top: 0;
        }
    }

    &__form {
        margin-top: 24px;
        display: flex;
        flex-wrap: wrap;
        transition: opacity 0.5s ease;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: center;
        }
        
        &:first-child{
            margin-top: 0;
        }

        &[disabled],
        &.is-disabled {
            cursor: default;
            pointer-events: none;
            opacity: 0.5;
        }
    }


    &__row {
        display: flex;
        flex-wrap: wrap;
        width: auto;
        width: calc(100% + 32px);
        flex-grow: 1;
        margin: 0 -16px;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            width: 100%;
            margin-right: 0;
            margin-left: 0;
        }

        &:last-child{
            margin-bottom: -16px;
        }
    }

    &__row + &__row{
        margin-top: 16px;
    }

    &__col{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        box-sizing: border-box;
        width: 100%;
        max-width: 50%;
        padding: 0 16px;
        flex-grow: 1;

        @media only screen and (max-width: $bp-md) {
            padding: 0;
            max-width: none;
        }

        &--aside{
            align-items: center;
            flex: 0 0 auto;
            width: 212px;

            @media only screen and (max-width: $bp-md) {
                width: 100%;
            }
        }
    }

    &__col--aside + &__col,
    &__col--aside + &__col + &__col {
        max-width: calc((100% - 212px) / 2);

        @media only screen and (max-width: $bp-md) {
            max-width: none;
        }
    }

    &__col--aside &__button{
        width: 100%;

        @media only screen and (max-width: $bp-md) {
            width: auto;
            height: 40px;
        }
    }

    &__col-title{
        color: $dark;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 12px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 8px;
        }
    }

    &__input-img {
        cursor: pointer;
        position: relative;
        display: flex;
        align-items: center;
        flex-shrink: 0;
        fill: $light;
        margin-bottom: 8px;

        &:hover{
            .icon{
                bottom: 32px;
            }
        }

        input{
            display: none;
        }

        .icon{
            position: absolute;
            left: 50%;
            bottom: 24px;
            width: 20px;
            height: 20px;
            transform: translateX(-50%);
            z-index: 2;
            transition: bottom 0.35s ease;
        }
    }

    &__img {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        width: 156px;
        height: 156px;
        border-radius: 50%;
        flex-shrink: 0;
        border: 4px solid $light;
        box-shadow: 0px 25px 50px -12px rgba(16, 24, 40, 0.1);
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__input {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 100%;
        margin-bottom: 16px;

        .input-img{
            width: 40px;
            height: 40px;
            margin-right: 16px;

            &__icon{
                width: 16px;
                height: 16px;
            }
        }

        .input{
            width: auto;
            flex-grow: 1;
            max-width: none;

            &__field{
                &[disabled]{
                    pointer-events: none;
                    border-color: transparent;
                    background-color: transparent;
                    padding: 0;
                }
            }
        }

        .button{
            width: 40px;
            height: 40px;
            margin-left: 16px;
            flex-shrink: 0;
        }

        .checkbox{
            min-height: 40px;
            align-items: center;
        }
    }

    &__input-title {
        width: 100%;
        color: #6B7280;
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 4px;
    }

    &__button{
        margin: 8px 0 16px;
    }
}



.account-section {
    overflow: hidden;

    &.is-hide &__trigger{
        transform: rotate(180deg);
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__title{
        color: $dark;

    }

    &__actions {
        display: flex;
        align-items: center;
        justify-content: flex-end;

        &>*{
            margin-left: 16px;
        }
    }

    &__add {
        flex-shrink: 0;
        font-weight: 600;
        font-size: 16px;
        line-height: 100%;

        span{
            @media only screen and (max-width: $bp-md) {
                display: none;
            }
        }

        .icon{
            width: 24px;
            height: 24px;
        }
    }

    &__trigger {
        width: 40px;
        height: 40px;
        padding: 0;
        border-radius: 50%;
        flex-shrink: 0;
        transition: transform 0.35s cubic-bezier(.77,0,.175,1), color 0.35s ease, fill 0.35s ease, border-color 0.35s ease, background-color 0.35s ease, opacity 0.35s ease, box-shadow 0.35s ease;

        .button{

            &__icon{
                width: 24px;
                height: 24px;
            }
        }
    }


    &__body {
        transition: max-height 0.5s cubic-bezier(.77,0,.175,1);
    }

    &__body-inner {
        box-sizing: border-box;
        padding-top: 24px;

        @media only screen and (max-width: $bp-lg) {
            padding-top: 16px;
        }
    }

    &__filter {
        margin-bottom: 20px;

        &:last-child{
            margin-bottom: 0;
        }

        .badges{
            
            .badge{
                height: 40px;
                font-size: 16px;
                font-weight: 600;
    
                &__wrapper{
                    padding: 0 18px;
    
                    .icon{
                        width: 24px;
                        height: 24px;
                    }
    
                    &>span span{
                        color: #D1D5DB;
                    }
                }
            }
        }
    }

    &__content {
        padding: 24px;
        background-color: $light;

        @media only screen and (max-width: $bp-lg) {
            padding: 20px;
            margin-right: -20px;
            margin-left: -20px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 16px;
            margin-left: -16px;
            margin-right: -16px;
        }

        .grid {

            &__block {
            }
        
            &__list {
                row-gap: 24px;

                .account-member{
                    grid-column: 1 / -1;
                }
            }
        
            &__more {
                font-size: 16px;
            }
        }
    }
}

.account-member {
    box-sizing: border-box;
    display: flex;
    background-color: #fff;
    color: $dark;
    width: 100%;
    padding: 8px 16px 8px 0;
    grid-column: 1 / -1;

    @media only screen and (max-width: $bp-lg) {
        padding: 16px;
        border: 1px solid #E5E7EB;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 12px;
    }

    form{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: initial;
        }
    }

    &__main {
        flex-grow: 1;
        display: flex;
        align-items: center;
    }

    &__img {
        pointer-events: none;
        width: 76px;
        height: 76px;
        overflow: hidden;
        margin-right: 16px;

        @media only screen and (max-width: $bp-lg) {
            width: 64px;
            height: 64px;
            margin-right: 12px;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 14px;
        line-height: 22px;
        color: #6B7280;
    }

    &__name{
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        animation: fadeIn 0.35s ease forwards;
    }

    &__input {
        display: none;
        max-width: 300px;
    }

    &__actions {
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: initial;
            
            .button{
                margin-bottom: 8px;
                height: 40px;
                
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }

    &__button-edit {
        display: none;
        margin-left: 10px;

        @media only screen and (max-width: $bp-md) {
            margin-left: 0;
        }
    }

    &__button {

        @media only screen and (min-width: $bp-md + 1) {
            flex-shrink: 0;
            padding: 0;
            width: 32px;
            margin-left: 20px;
            background-color: transparent;
            border-color: transparent;
            color: #374151;
            fill: #374151;
    
            &:hover{
                background-color: rgba(221, 205, 255, 0.1);
                border-color: transparent;
            }
    
            &:active{
                background-color: rgba(215, 199, 255, 0.5);
                border-color: transparent;
            }
        }

        @media only screen and (max-width: $bp-md) {
            animation: fadeIn 0.35s ease forwards;
        }

        .button{
            &__text{
                @media only screen and (min-width: $bp-md + 1) {
                    display: none;
                }
            }

            &__icon{
                width: 24px;
                height: 24px;
                margin-left: 0 !important;

                @media only screen and (max-width: $bp-md) {
                    display: none;
                }
            }
        }
        

        &--delete { 
            fill: #DC2626;

            &:hover{
                fill: #991B1B;
            }

            @media only screen and (max-width: $bp-md) {
                background-color: #DC2626;
                color: #fff;
                fill: #fff;

                &:hover,
                &:active {

                    background-color: #991B1B;
                }
            }
        }
    }

        
    &.is-edit &__img{
        pointer-events: all;
    }

    &.is-edit &__input{
        display: flex;
        animation: fadeIn 0.35s ease forwards;
    }

    &.is-edit &__name{
        display: none;
    }

    &.is-edit &__button-edit{
        display: flex;
        animation: fadeIn 0.35s ease forwards;
    }
    
    &.is-edit &__button{
        @media only screen and (max-width: $bp-md) {
            display: none;
        }
    }
}


.account-career{
    color: $dark;
    width: 100%;

    &__list{
        display: flex;
        flex-wrap: wrap;
        margin: 0 -16px -16px;

        @media only screen and (max-width: $bp-md) {
            margin-right: -8px;
            margin-left: -8px;
        }
    }

    &__item{
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        padding: 0 16px;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-md) {
            padding: 0 8px;
            flex-basis: 50%;
            flex-grow: 1;
        }
    }

    &__title{
        font-weight: 600;
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 8px;

        @media only screen and (max-width: $bp-lg) {
            color: #6B7280;
        }
    }

    &__content{

        span{
            font-weight: 700;
            font-size: 18px;
            line-height: 24px;

            @media only screen and (max-width: $bp-lg) {
                font-size: 16px;

            }
        }
    }

    &__rating{
        display: flex;
        align-items: center;

        .icon{
            margin-right: 5px;
            fill: #FAAD14;
        }

        span{

        }
    }
}