@import "@theme/vars.scss";

.map{
    width: 100%;
    height: 400px;

    &__content{
        width: 100%;
        height: 100%;
    }

    &__dot{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: $blue;
        transform: translate(-50%, -50%);
        transition: 0.35s ease;

        &.is-active{
            background-color: red;
        }
    }
}