@import "@theme/vars.scss";

.cookies {
    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    right: 0;
    width: 100%;
    z-index: 100;
    padding-right: inherit;
    opacity: 0;
    visibility: hidden;
    transform: translateY(100%);
    transition: visibility 0.5s ease, opacity 0.5s ease, transform 0.5s ease;

    .container{
        @media only screen and (max-width: $bp-md) {
            padding: 0;
        }
    }

    &.is-show{
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
    }

    &__inner {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        min-height: 98px;
        background-color: $light-light;
        padding: 16px;
        color: $dark;
        
        @media only screen and (max-width: $bp-md) {
            min-height: 88px;
            padding: 20px;
            flex-direction: column;
        }
    }

    &__text {
        margin-right: 24px;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: 5px;
        }
    }

    &__button {
       flex-shrink: 0;
       white-space: nowrap;

       @media only screen and (max-width: $bp-md) {
           margin-top: 16px;
           width: 100%;
       }
    }
}