@import "@theme/vars.scss";

.importantly {

    .container{
        @media only screen and (max-width: $bp-lg) {
            padding: 0;
        }
    }

    &--books &__img{
        object-fit: contain;
        object-position: center;
    }

    &__slider {
        .slider {
            &__inner {
                overflow: visible;
            }
        
            &__slide {
                opacity: 0.4;
                transition: opacity 0.5s ease 0.2s;

                &.swiper-slide-active{
                    opacity: 1;
                }
            }
        
            &__navigation {
                position: absolute;
                bottom: 40px;
                right: 56px;
                
            }
        }
    }

    &__item {
        box-sizing: border-box;
        position: relative;
        cursor: pointer;
        display: block;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        width: 100%;
        height: auto;
        min-height: 500px;
        padding: 40px 56px;
        text-decoration: none;
        color: $light;
        overflow: hidden;

        @media only screen and (max-width: $bp-xl) {
            padding: 32px 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            min-height: 400px;
            padding: 24px;
        }

        &::after{
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            opacity: 0.75;
            background: linear-gradient(0deg, rgba(17, 24, 39, 0.30), rgba(17, 24, 39, 0.30)), linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 100%);
            transition: opacity 0.5s ease;
        }

        &:hover::after{
            opacity: 1;
        }
    }

    &__item:hover &__img{
        transform: scale(102%);
    }

    &__img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
        will-change: transform;
        transition: transform 0.75s ease;
    }

    &__item-inner {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        height: 100%;
        flex-grow: 1;
    }

    &__subtitle {
        font-weight: 600;
        margin-bottom: 16px;
    }

    &__main {
        width: 100%;
        
        @media only screen and (max-width: $bp-lg) {
            margin-bottom: auto;
        }

        &>*{
            &:last-child{
                margin-bottom: 0;
            }
        }
    }

    &__labels {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        max-width: 100%;
        margin: 0 -4px;
    }

    &__label {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 24px;
        padding: 0 8px;
        margin: 0 4px 16px;
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        border-radius: 20px;
        color: $orange;
        border: 1px solid $orange;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
        }
        
        &:first-of-type{
            background-color: $orange;
            color: $light;
        }
    }

    &__title {
        margin-bottom: 16px;
        color: #ECECEC;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        
        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 8px;
            font-weight: 700;
            font-size: 34px;
            line-height: 42px;
            margin-bottom: 8px;
        }
    }

    &__title + &__text{
        margin-top: 8px;
    }

    &__text{
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        font-weight: 300;
        font-size: 24px;
        line-height: 140%;
        color: $light;

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
            -webkit-line-clamp: 2;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 16px;
        }
    }

    &__info {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    &__info-item {
        display: inline-flex;
        align-items: center;
        font-size: 16px;
        line-height: 16px;
        color: #F5F5F5;
        fill: #F5F5F5;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            margin-bottom: 8px;
        }

        .icon{
            width: 16px;
            height: 16px;
        }

        .icon + span{
            margin-left: 8px;
        }

        span + .icon{
            margin-left: 8px;
        }

        &::after{
            content: "";
            display: inline-flex;
            background-color: $light;
            width: 3px;
            height: 3px;
            border-radius: 50%;
            margin: 0 12px;

            @media only screen and (max-width: $bp-lg) {
                margin: 0 8px;
            }
        }

        &:last-of-type::after{
            content: none;
        }
    }

    &__button {
        margin-top: 16px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 24px;
        }
    }
}
