@import "@theme/vars.scss";

.search {
    position: relative;
    z-index: 10;
    box-sizing: border-box;
    background: none;
    border: none;
    outline: none;
    display: flex;
    flex-grow: 1;
    height: 50px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    transition: padding 0.35s ease;

    @media only screen and (max-width: $bp-lg) {
        font-size: 16px;
        height: 38px;
        max-width: none;
        pointer-events: none;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 14px;
    }

    form{
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
  
    }


    &__field{
        outline: none;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        padding: 0 45px 0 20px;
        background-color: #E6E8F2;
        border-radius: 50px;
        color: $dark-light;
        border: none !important;
        transition: background-color 0.35s ease,color 0.35s ease, opacity 0.35s cubic-bezier(.77,0,.175,1);

        @media only screen and (max-width: $bp-lg) {
            padding: 0 36px 0 16px;
            opacity: 0;
            // background-color: $blue-medium;
            // color: #FFFFFF;
        }

        &:focus{
            background-color: #D7C7FF;
   
            // @media only screen and (max-width: $bp-lg) {
            //     color: $dark-light;
            // }
        }

        &::placeholder{
            color: #6B7280;
            opacity: 1;

            // @media only screen and (max-width: $bp-lg) {
            //     color: #fff;
            // }
        }
    }

    &.is-open &__field{
        @media only screen and (max-width: $bp-lg) {
            pointer-events: all;
            opacity: 1; 
            transition-delay: 0s, 0s, 0.25s;
        }
    }

    &__button{
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background-color: transparent;
        fill: $blue;
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
        transition: background-color 0.35s ease, fill 0.35s ease, right 0.35s cubic-bezier(.77,0,.175,1);

        &:hover{
            background-color: $blue;
            fill: $light;
        }
        
        @media only screen and (max-width: $bp-lg) {
            pointer-events: all;
            right: 0;
            width: 32px;
            height: 32px;
            background-color: transparent;
            transition-delay: 0s, 0s, 0.25s;

            &:hover{
                fill: $blue;
                background-color: transparent;
            }
        }

    }

    &.is-open &__button{
        @media only screen and (max-width: $bp-lg) {
            right: 16px;
        }
    }

    &__icon{
        width: 16px;
        height: 16px;
    }

    &__dropdown{
        display: none;
        position: absolute;
        top: 100%;
        right: 0;
        width: 600px;
        z-index: 20;
        opacity: 1; 
        visibility: visible;
        transform: translateY(8px);

        @media only screen and (max-width: $bp-xl) {
            width: 100%;
        }

        @media only screen and (max-width: $bp-lg) {
            transform: none;
        }
    }

    // .is-focus &__dropdown{
    //     opacity: 1; 
    //     visibility: visible;
    //     transform: translateY(8px);

    //     @media only screen and (max-width: $bp-lg) {
    //         transform: none;
    //     }
    // }
}

.search-dropdown {
    box-sizing: border-box;
    padding: 24px 0 16px;
    height: auto;
    background-color: #fff;
    border: 1px solid #D1D5DB;
    transition: opacity 0.4s ease, visibility 0.4s ease, transform 0.6s ease;

    &__inner {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__list {
        padding: 0 16px;
        overflow-y: auto;
        max-height: 500px;

        // @media only screen and (max-width: $bp-lg) {
        //     max-height: 400px;
        // }
    }

    &__block {
        display: flex;
        flex-direction: column;
        margin-bottom: 8px;

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__block-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        min-height: 32px;
        position: sticky;
        top: -1px;
        background-color: #fff;
        z-index: 10;
        margin-bottom: 8px;
    }

    &__title {
        color: $dark;

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 24px;
        }
    }

    &__link {
        padding: 8px 16px;
        font-size: 14px;    
        line-height: 14px;

        .icon{
            width: 16px;
            height: 16px;
        }
    }

    &__block-list {
        display: flex;
        flex-direction: column;

        & > *{
            margin-bottom: 16px;

            &:last-child{
                margin-bottom: 0;
            }
        }
    }

    &__button {
        flex-grow: 1;
        margin: 0 16px;
        margin-top: 16px;
        height: 44px;
        font-size: 16px;
    }
}
