@import "@theme/vars.scss";

html {
  font-family: $defaultFontFamily;
  font-size: $defaultFontSize;
  line-height: $defaultLineHeight;
  color: $blue-dark;
  background-color: #FFF;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow-x: hidden;

    &._lock {
        overflow: hidden;
    }

    
    &::after{
        content: "";
        width: 100%;
        height: 100%;
        background-color: #fff;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 100;
        transition: opacity 0.35s ease;
        opacity: 1;
    }
    &.is-loaded{
        &::after{
            opacity: 0;
            pointer-events: none;
            transition-delay: 0.15s;
        }
    }
}

*{
    -webkit-tap-highlight-color: transparent;
    scrollbar-color: $blue #DEE5E8;
    scrollbar-width: thin;
    
    &::-webkit-scrollbar {
        height: 8px;
        width: 8px;
    }

    &::-webkit-scrollbar-track {
        background-color: $light;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $blue;
    }
}

img{
  image-rendering: -webkit-optimize-contrast;
}

.main{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    width: 100%;

    // .breadcrumbs + section{
    //     padding-top: 0;
    // }

    section,
    article {
        overflow-x: hidden;

        &:first-child{
            padding-top: 56px;

            @media only screen and (max-width: $bp-lg) {
                padding-top: 48px;
            }
        }

        &:last-child{
            padding-bottom: 56px;

            @media only screen and (max-width: $bp-lg) {
                padding-bottom: 48px;
            }
        }
    }
}

.section{
    box-sizing: border-box;
    padding-bottom: 56px;

    @media only screen and (max-width: $bp-lg) {
        padding-bottom: 48px;
    }

    &--visible{
        overflow-x: visible !important;
    }

    &--aside{
        overflow-x: visible !important;
    }

    &--top{
        padding-top: 50px;

        @media only screen and (max-width: $bp-xxl) {
            padding-top: 40px;
        }

        @media only screen and (max-width: $bp-xl) {
            padding-top: 32px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding-top: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding-top: 16px;
        }
    }

    &__header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 32px;
        
        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            flex-wrap: wrap;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__header-button{
        margin-left: 16px;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin: 8px 0 0;
        }
    }

    &__filter{
        margin-top: 24px;
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 32px;
        }

        &:first-child{
            margin-top: 0;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__header &__filter {
        margin: 0;
        flex-grow: 0;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            margin-top: 24px;
        }

        &>form{
            flex-grow: 0;

            @media only screen and (max-width: $bp-md) {
                flex-grow: 1;
            }
        }
    }
    
    &__title{
        box-sizing: border-box;
        color: $blue;
        margin-right: auto;
        padding-right: 16px;

        @media only screen and (max-width: $bp-lg) {
            max-width: calc(100% - 32px);
            padding-right: 8px;
        }

        @media only screen and (max-width: $bp-md) {
            padding-right: 0;
        }
    }

    &__link{
        font-weight: 600;
        font-size: 24px;
        line-height: 32px;

        @media only screen and (max-width: $bp-lg) {
            padding: 8px;
        }

        span{
            @media only screen and (max-width: $bp-lg) {
                display: none;
            }
        }
        
        .icon{
            @media only screen and (max-width: $bp-lg) {
                width: 16px;
                height: 16px;
                margin-left: 0 !important;
            }
        }
    }

    &__subtitle {
        margin-top: 32px;
    }
    
    &__badges{
        box-sizing: border-box;
        margin-top: 20px;

        margin-right: -72px;
        padding-left: 72px;
        margin-left: -72px;
        padding-right: 72px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: -40px;
            padding-left: 40px;
            margin-left: -40px;
            padding-right: 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-top: 15px;
            margin-right: -25px;
            padding-left: 25px;
            margin-left: -25px;
            padding-right: 25px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: -20px;
            padding-left: 20px;
            margin-left: -20px;
            padding-right: 20px;
        }


        -webkit-tap-highlight-color: transparent;
        scrollbar-color: transparent transparent;
        scrollbar-width: thin;
        
        &::-webkit-scrollbar-track {
            background-color: transparent;
        }

        &::-webkit-scrollbar-thumb {
            background-color: transparent;
        }
    }

    &__inner{
        display: flex;
        flex-direction: column;
        position: relative;
        z-index: 1;
    }
    
    &--aside &__inner{
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__body{
        width: calc(100% - 442px);

        @media only screen and (max-width: $bp-xxl) {
            width: calc(100% - 400px);
        }

        @media only screen and (max-width: $bp-xl) {
            width: calc(100% - 350px);
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }

        .grid{
            &__title{
                @media only screen and (max-width: $bp-lg) {
                    margin-bottom: 24px;
                }

                @media only screen and (max-width: $bp-md) {
                    font-size: 24px;
                    line-height: 32px;
                }
            }

            &__more{
                margin-top: 16px;
                width: 100%;
                font-size: 14px;
                height: 40px;

                background-color: $light;
                color: $dark-light;
                fill: $dark-light;
                border: 1px solid #E5E7EB;
        
                &:hover{
                    background-color: $light-light;
                }
        
                &:active{
                    background-color: #E5E7EB;
                    border-color: #D1D5DB;
                }

                .button{
                    &__icon {
                        width: 16px;
                        height: 16px;
                    }
                }
            }
        }
    }

    &__aside {
        display: block;
        width: 402px;
        flex-shrink: 0;
        margin-left: auto;

        @media only screen and (max-width: $bp-xxl) {
            width: 360px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 320px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-top: 24px;
        }
    }

    &__aside-inner {
        display: flex;
        flex-direction: column;
        position: sticky;
        top: 168px;
        background-color: #fff;
        max-height: calc(100vh - 200px);

        @media only screen and (max-width: $bp-lg) {
            max-height: none;
            position: relative;
            background-color: transparent;
            top: 0;
        }
    }

    &__aside-title {
        font-weight: 600;
        color: $dark-light;
        padding-bottom: 12px;
        margin-bottom: 12px;
        width: 100%;
        position: sticky;
        top: 0px;
        z-index: 4;
        background-color: #fff;

        @media only screen and (max-width: $bp-lg) {
            position: static;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
        }
    }

    &__aside-list {
        display: flex;
        flex-wrap: wrap;
        overflow-y: auto;

        @media only screen and (max-width: $bp-lg) {
            overflow: hidden;
        }

        .card{
            width: 100%;
            margin-bottom: 16px;

            &:last-of-type{
                margin-bottom: 0;
            }
        }
    }

    &__aside-button {
        margin-top: 16px;
        width: 100%;
        justify-content: space-between;
        z-index: 5;
        flex-shrink: 0;
    }


    &__result-nav{
        width: 100%;
        overflow-x: auto;
        padding-bottom: 12px;
        margin-bottom: 36px;
        position: sticky;
        top: 168px;
        z-index: 10;
        background-color: #fff;
        scrollbar-width: thin;

        @media only screen and (max-width: $bp-lg) {
            top: 70px;
            margin-bottom: 20px;

            &::-webkit-scrollbar {
                height: 0px;
                width: 0px;
            }
        }
    }

    &__result-nav-list{
        display: flex;
        align-items: center;
    }

    &__result-link{
        box-sizing: border-box;
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 8px 12px;
        text-decoration: none;
        color: $dark;
        font-weight: 600;
        font-size: 16px;
        line-height: 28px;
        border-bottom: 1px solid #E5E7EB;
        transition: border-color 0.35s ease;
        white-space: nowrap;

        &:hover{
            border-color: $blue-white;
        }

        &.is-active{
            border-color: $blue;
        }

        span{
            box-sizing: border-box;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            padding: 0 6px;
            min-width: 20px;
            width: 20px;
            border-radius: 50%;
            background-color: #E5E7EB;
            font-size: 14px;
            line-height: 22px;
            margin-left: 8px;
        }
    }

    &__result-nothing{
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        width: 100%;
        color: $dark;
        padding: 32px;

        @media only screen and (max-width: $bp-lg) {
            padding: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 16px;
        }
        
        img{
            width: 300px;
            height: 300px;
            margin-bottom: 16px;

            @media only screen and (max-width: $bp-lg) {
                width: 200px;
                height: 200px;
            }
        }

        .h3{
            font-weight: 700;
            margin-bottom: 8px;
        }
    }

}


.container {
    box-sizing: border-box;
    width: 100%;
    max-width: 1280px;
    padding: 0 20px;
    margin: 0 auto;

    @media only screen and (max-width: $bp-lg) {
        padding: 0 16px;
    }
}