@import "@theme/vars.scss";

.section-404{
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    &__block{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: $dark;
        padding:0 20px;
        max-width: 660px;
        text-align: center;
        margin: 0 auto;

        img{
            display: block;
            width: 100%;
            max-width: 400px;
            margin-bottom: 24px;

            @media only screen and (max-width: $bp-lg) {
                max-width: 320px;
            }
        }

        .h2{
            margin-bottom: 12px;
        }

        .h4{
            margin-bottom: 24px;
        }

        .button{
            min-width: 200px;
        }
    }
}