@import "@theme/vars.scss";

.list {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
    padding-left: 24px;

    @media only screen and (max-width: $bp-lg) {
        padding-left: 12px;
    }

    &__item {
        display: flex;
        align-items: flex-start;
        color: $blue;
        padding: 0;
        margin-bottom: 24px;

        &:last-child{
            margin-bottom: 0;
        }

        &::before{
            content: "";
            display: inline-flex;
            flex-shrink: 0;
            width: 8px;
            height: 8px;
            background-color: $blue;
            margin: 8px 12px 8px 0;

            @media only screen and (max-width: $bp-lg) {
                width: 6px;
                height: 6px;
                margin: 9px 12px 9px 0;
            }
        }
    }
}