@import "@theme/vars.scss";
.contacts {
    margin-top: 48px;
    
    @media only screen and (max-width: $bp-lg) {
        margin-top: 32px;
    }

    &__title{
        color: $dark;
        margin-bottom: 24px;
    }

    &__inner{
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        background-color: $light-light;
        padding: 24px;
    
        @media only screen and (max-width: $bp-lg) {
            padding: 16px;
            flex-direction: column;
        }

        &::after{
            content: "";
            display: block;
            width: 1px;
            margin: 0 24px;
            background-color: #D1D5DB;
            order: 2;
            flex-shrink: 0;

            @media only screen and (max-width: $bp-lg) {
                margin: 16px 0;
                width: 100%;
                height: 1px;
            }
        }
    }


    &__form {
        order: 1;
        flex: 1 1 50%;
        min-width: 420px;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            min-width: auto;
        }

        form{

        }
    }

    &__input {
        margin-bottom: 16px;
        
        &:last-child{
            margin-bottom: 0;
        }

        .input{
            max-width: none;

            &--textarea .input__field{
                height: 244px;

                @media only screen and (max-width: $bp-lg) {
                    height: 168px;
                }
            }
        }
    }

    &__input-title {
        font-size: 16px;
        line-height: 28px;
        color: #6B7280;
        margin-bottom: 4px;
    }

    &__button {
        width: 100%;
        height: 44px;
        padding: 0 16px;
        font-size: 16px;    

        .button__icon{
            width: 24px;
            height: 24px;
        }
    }

    &__info {
        order: 3;
        flex-grow: 1;
        flex: 1 1 50%;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }
    }

    &__info-item {
        display: flex;
        flex-direction: column;
        margin-bottom: 16px;

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__info-title {
        font-size: 16px;
        line-height: 28px;
        margin-bottom: 8px;
        color: #6B7280;
    }

    &__link {
        display: inline-flex;
        align-items: center;
        text-decoration: none;
        color: $dark;
        font-weight: 600;
        font-size: 20px;
        line-height: 32px;
        transition: color 0.35s ease;

        &:hover{
            color: $blue;
        }
    }

    &__map {
        width: 100%;
        height: 200px;
        margin-top: 16px;
    }
}
