@import "@theme/vars.scss";


.article {
    overflow: initial !important;

    &__inner {
        position: relative;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__actions {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        width: calc(100% + 16px);
        margin: 24px -8px -8px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 16px;
        }
    }

    &__action {
        margin: 0px 8px 8px;

        @media only screen and (max-width: $bp-lg) {
            flex-grow: 1;
            flex-basis: calc(50% - 16px);
        }
        
    }

    &__body {
        margin-top: 32px;
        width: calc(100% - 442px);

        @media only screen and (max-width: $bp-xxl) {
            width: calc(100% - 400px);
        }

        @media only screen and (max-width: $bp-xl) {
            width: calc(100% - 350px);
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }

        & > *{
            &:first-child{
                margin-top: 0;
            }
        }
    }

        &__content {
            color: $dark-light;
            text-align: justify;

            [class^='card'] {
                font-family: 'Mont';
            }

            &>[class^='card']{
                margin: 24px 0;
            }

            &>*{
                &:first-child{
                    margin-top: 0;
                }

                &:last-child{
                    margin-bottom: 0;
                }
            }

            h1:not([class^='card']), h2:not([class^='card']), h3:not([class^='card']), h4:not([class^='card']), h5:not([class^='card']) {
                font-family: 'Mont';
                font-weight: 600;
            }

            h2:not([class^='card']){
                font-size: 24px;
                line-height: 32px;
                margin-bottom: 32px;

                @media only screen and (max-width: $bp-lg) {
                    font-size: 22px;
                    line-height: 27px;
                }

                @media only screen and (max-width: $bp-md) {
                    font-size: 18px;
                    line-height: 24px;
                }
            }

            h3:not([class^='card']){
                font-size: 20px;
                line-height: 135%;
            
                @media only screen and (max-width: $bp-lg) {
                    font-size: 18px;
                }
            
                @media only screen and (max-width: $bp-md) {
                    font-size: 16px;
                }
            }

            p:not([class^='card']){
                font-size: 16px;
                line-height: 24px;
                margin-bottom: 24px;
                
                @media only screen and (max-width: $bp-lg) {
                    font-size: 14px;
                    line-height: 22px;

                }

                &:last-child{
                    margin-bottom: 0;
                }

                span{
                    font-weight: 600;
                }

                strong{
                    font-weight: 700;
                }

                a{
                    color: #1890FF;
                    text-decoration: none;
                    transition: color 0.35s ease;

                    &:hover{
                        color: $blue-medium;
                        fill: $blue-medium;
                    }

                }
            }

            figure{
                box-sizing: border-box;
                position: relative;
                margin: 56px 0;
                padding: 32px 96px;
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                background-size: 100%;
                background-repeat: no-repeat;
                background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 797 128' fill='none' xmlns='http://www.w3.org/2000/svg' preserveAspectRatio='none'%3E%3Cpath d='M49.7334 0L797 8L767.945 112L0 128L49.7334 0Z' fill='%23151F6D'/%3E%3C/svg%3E%0A");
                font-family: 'Mont';
                text-align: center;

                @media only screen and (max-width: $bp-lg) {
                    margin: 40px 0;
                }

                &::before{
                    content: "";
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 96px;
                    height: 96px;
                    z-index: 1;
                    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 96 96' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cmask id='mask0_407_68903' style='mask-type:alpha' maskUnits='userSpaceOnUse' x='0' y='0' width='96' height='96'%3E%3Crect width='96' height='96' fill='%23D9D9D9'/%3E%3C/mask%3E%3Cg mask='url(%23mask0_407_68903)'%3E%3Cpath d='M60.5 71L68.5 55H52.5V27H80.5V55L72.5 71H60.5ZM24.5 71L32.5 55H16.5V27H44.5V55L36.5 71H24.5Z' fill='%239CA3AF'/%3E%3C/g%3E%3C/svg%3E%0A");

                    @media only screen and (max-width: $bp-lg) {
                        width: 64px;
                        height: 64px;
                    }
                }

                blockquote{
                    position: relative;
                    z-index: 2;
                    color: #fff;
                    margin: 0;
    
                    p{
                        font-weight: 600;
                        font-size: 18px;
                        line-height: 26px;
                        font-style: italic;

                        @media only screen and (max-width: $bp-lg) {
                            font-size: 16px;
                            line-height: 24px;
                        }
                    }
                }

                figcaption{
                    position: relative;
                    z-index: 2;
                    margin: 16px 0 0;
                    color: #D1D5DB;

                    p{
                        font-size: 14px;
                        line-height: 22px;
                    }
                }
            }

            figure.image{
                padding: 0;
                background: none;

                &::before{
                    content: none;
                }
                
                img{
                    margin: 0 !important;
                }

                figcaption{
                    color: #6B7280;
                }
            }

            &>blockquote{
                margin: 32px 0;
                padding: 32px;
                background-color: $light-light;
                display: flex;
                flex-direction: column;
                font-family: 'Mont';
                color: #1F2937;

                @media only screen and (max-width: $bp-xl) {
                    padding: 24px;
                }

                @media only screen and (max-width: $bp-lg) {
                    padding: 16px;
                    margin: 24px 0;
                }
            }

            ul{
                margin: 32px 0;
                padding-left: 24px;

                @media only screen and (max-width: $bp-lg) {
                    padding-left: 12px;
                }

                li{
                    display: flex;
                    margin-bottom: 24px;
                    
                    &::before{
                        content: "";
                        display: inline-flex;
                        flex-shrink: 0;
                        width: 8px;
                        height: 8px;
                        background-color: $blue;
                        margin: 8px 12px 8px 0;

                        @media only screen and (max-width: $bp-lg) {
                            width: 6px;
                            height: 6px;
                            margin: 9px 12px 9px 0;
                        }
                    }

                    &:last-of-type{
                        margin-bottom: 0;
                    }
                }
            }

            ol{
                margin: 32px 0;
                padding-left: 44px;

                @media only screen and (max-width: $bp-lg) {
                    padding-left: 30px;
                }
                
                li{
                    margin-bottom: 24px;

                    &::marker{
                        font-family: 'Mont';
                        font-weight: 600;
                        font-size: 14px;
                    }
                }
            }

            img:not([class^='card'] img){
                display: flex;
                overflow: hidden;
                width: 100%;
                flex-grow: 1;
                // height: 400px;
                margin: 20px 0;
                object-fit: cover;

                @media only screen and (max-width: $bp-lg) {
                    // height: 300px;
                }
            }
        }

            &__border {
                margin: 32px 0;
                padding: 32px;
                // border: 1px solid #D1D5DB;
                background-color: $light-light;
                display: flex;
                flex-direction: column;
                font-family: 'Mont';
                color: #1F2937;

                @media only screen and (max-width: $bp-xl) {
                    padding: 24px;
                }

                @media only screen and (max-width: $bp-lg) {
                    padding: 16px;
                    margin: 24px 0;
                }

                p{
                    
                }
            }

            &__images {
                display: flex;
                margin: 32px 0;

                @media only screen and (max-width: $bp-md) {
                    flex-direction: column;
                }
            }

            &__slider {
                margin: 24px 0 40px;
                
                @media only screen and (max-width: $bp-lg) {
                    margin: 32px 0;
                }

                .slider {

                    &__navigation {
                        position: absolute;
                        right: 32px;
                        bottom: 32px;

                        @media only screen and (max-width: $bp-lg) {
                            display: none;
                        }
                    }
                }
            }

            &__slider img{
                margin: 0 !important;
            }

            &__grid{
                padding-top: 24px;
                margin: 24px 0;
                border-top: 1px solid #D1D5DB;
    
                &:first-child{
                    padding-top: 0;
                    border-top: 0;
                    margin-top: 0;
    
                    @media only screen and (max-width: $bp-lg) {
                        padding-top: 8px;
                    }
                }
    
                .grid{
    
                    &__title{
                        margin-bottom: 16px;
                        font-size: 24px;
                        line-height: 32px;
                    
                        @media only screen and (max-width: $bp-lg) {
                            font-size: 22px;
                            line-height: 27px;
                        }
                    
                        @media only screen and (max-width: $bp-md) {
                            font-size: 20px;
                            line-height: 25px;
                        }
                    }
    
                    &__block{
                        padding-bottom: 24px;
                        margin-bottom: 24px;
                        border-bottom: 1px solid #D1D5DB;
                    }
    
                    &__more{
                        margin-top: 16px;
                        width: 100%;
                        font-size: 14px;
                        height: 40px;
    
                        background-color: $light;
                        color: $dark-light;
                        fill: $dark-light;
                        border: 1px solid #E5E7EB;
                
                        &:hover{
                            background-color: $light-light;
                        }
                
                        &:active{
                            background-color: #E5E7EB;
                            border-color: #D1D5DB;
                        }
    
                        .button{
                            &__icon {
                                width: 16px;
                                height: 16px;
                            }
                        }
                    }
                }
                
            }
    
            &__cards{
                margin: 32px 0;
    
                @media only screen and (max-width: $bp-lg) {
                    margin: 24px;
                }
    
                &>*{
                    margin-bottom: 16px;
                    
                    &:last-of-type{
                        margin-bottom: 0;
                    }
                }
            }
    
            &__set{
                padding: 32px 0;
                border-bottom: 1px solid #D1D5DB;
    
                &:first-child{
                    padding-top: 0;
                }
    
                &>h2{
                    margin-bottom: 24px !important;
                } 
                
                &>[class^='card'] {
                    margin-bottom: 24px;
                }
            }

            &__info{
                margin: 24px 0;
                p {
                    color: #9CA3AF;
                    font-size: 16px;
                    line-height: 28px;

                    a{
                        text-decoration: none;
                        color: #1890FF;
                        transition: color 0.35s ease;

                        &:hover{
                            color: $blue;
                        }
                    }
                }
            }

        &__labels {
            display: flex;
            flex-direction: column;
            margin: 24px 0;
            
        }

        &__labels-title{
            font-weight: 600;
            font-size: 24px;
            line-height: 32px;
            letter-spacing: -0.02em;
            margin-bottom: 16px;
        }

        &__labels-list{
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            overflow: hidden;
            margin: 0 -4px -8px;
        }
        
        &__label {
            position: relative;
            box-sizing: border-box;
            display: inline-flex;
            align-items: center;
            height: 24px;
            padding: 0 8px;
            border-radius: 50px;
            border: 1px solid $orange;
            text-decoration: none;
            color: $orange;
            fill: $orange;
            font-weight: 600;
            font-size: 14px;
            line-height: 100%;
            margin: 0 4px 8px;
            max-width: 100%;
            overflow: hidden;
            flex-shrink: 0;
            transition: color 0.35s ease, border-color 0.35s ease;
    
            &:nth-last-child(1),
            &:nth-last-child(2){
                flex-shrink: 1;
            }
            &:first-of-type{
                flex-shrink: 0;
                background-color: $orange;
                color: #fff;
                fill: #fff;
            }
    
            .icon{
                width: 16px;
                height: 16px;
                margin-right: 5px;
            }
    
            span{
                overflow: hidden;
                text-overflow: ellipsis;
                max-width: 100%;
                white-space: nowrap;
            }
        }

        &__author-block {
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            margin-top: 24px;
            padding: 24px 0;
            border: 1px solid #D1D5DB;
            border-width: 1px 0;

            &--lg{
                padding: 24px 16px;

                @media only screen and (max-width: $bp-lg) {
                    padding: 24px 0;
                }
            }
        }

        &__author-block-title{
            box-sizing: border-box;
            color: $blue-dark;
            margin-bottom: 24px;
            
            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 16px;
            }
        }

        &__author-list{
            flex-grow: 1;
            display: flex;
            flex-wrap: wrap;
            margin: 0 -8px -8px;
        }

        &__author-item{
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            width: 50%;
            padding: 0 8px;
            margin-bottom: 8px;

            @media only screen and (max-width: $bp-md) {
                width: 100%;
            }
        }

        &__author-title {
            display: flex;
            width: 100%;
            font-size: 16px;
            line-height: 100%;
            color: $light-medium;
            margin-bottom: 8px;
        }

        &__author {
            cursor: pointer;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            padding: 8px;
            text-decoration: none;
            font-weight: 600;
            color: $dark;
            transition: background-color 0.35s ease;

            @media only screen and (max-width: $bp-lg) {
                width: 100%;
            }

            &:hover{
                background-color: #E5E7EB;
            }

            span{
                font-size: 16px;
                line-height: 100%;
            }
        }

        &__author-img {
            display: flex;
            width: 32px;
            height: 32px;
            flex-shrink: 0;
            border-radius: 50%;
            margin-right: 12px;
            overflow: hidden;

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__comments {
            margin-top: 24px;
            width: 100%;
        }

    &__aside {
        display: block;
        width: 402px;
        flex-shrink: 0;
        margin-left: auto;
        margin-top: 32px;

        @media only screen and (max-width: $bp-xxl) {
            width: 360px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 320px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-top: 24px;
        }
    }

        &__aside-inner {
            display: flex;
            flex-direction: column;
            position: sticky;
            top: 168px;
            background-color: #fff;
            max-height: calc(100vh - 200px);

            @media only screen and (max-width: $bp-lg) {
                max-height: none;
                position: relative;
                background-color: transparent;
                top: 0;
            }
        }

        &__aside-title {
            font-weight: 600;
            color: $dark-light;
            padding-bottom: 12px;
            margin-bottom: 12px;
            width: 100%;
            position: sticky;
            top: 0px;
            z-index: 4;
            background-color: #fff;

            @media only screen and (max-width: $bp-lg) {
                position: static;
            }

            @media only screen and (max-width: $bp-md) {
                font-size: 18px;
            }
        }

        &__aside-list {
            display: flex;
            flex-wrap: wrap;
            overflow-y: auto;

            @media only screen and (max-width: $bp-lg) {
                overflow: hidden;
            }

            .card{
                width: 100%;
                margin-bottom: 16px;

                &:last-of-type{
                    margin-bottom: 0;
                }
            }
        }

        &__aside-button {
            margin-top: 16px;
            width: 100%;
            justify-content: space-between;
            z-index: 5;
            flex-shrink: 0;
        }

    &--places {
        .article-intro{
            min-height: auto;
            padding: 0;
            position: static;
            display: flex;
            flex-direction: column;
    
            &::after{
                content: none;
            }

            &__main{
                width: calc(100% - 442px);
                color: $blue;
                margin-right: 0;

                @media only screen and (max-width: $bp-xxl) {
                    width: calc(100% - 400px);
                }

                @media only screen and (max-width: $bp-xl) {
                    width: calc(100% - 350px);
                }

                @media only screen and (max-width: $bp-lg) {
                    width: 100%;
                    margin-top: 24px;
                }
            }

            &__slider{
                width: calc(100% - 442px);

                @media only screen and (max-width: $bp-xxl) {
                    width: calc(100% - 400px);
                }

                @media only screen and (max-width: $bp-xl) {
                    width: calc(100% - 350px);
                }

                @media only screen and (max-width: $bp-lg) {
                    order: 2;
                    min-width: 100%;
                    width: auto;
                }
            }
        
            &__title{
                font-size: 34px;
                line-height: 125%;
                
                @media only screen and (max-width: $bp-lg) {
                    font-size: 30px;
                }
            
                @media only screen and (max-width: $bp-md) {
                    font-size: 24px;
                    line-height: 32px;
                }
            }
        
            &__subtitle{
                font-weight: 400;
                font-size: 20px;
                line-height: 32px;
                margin-top: 10px;
                color: #4B5563;
        
                @media only screen and (max-width: $bp-lg) {
                    font-size: 18px;
                    line-height: 24px;
                    margin-top: 8px;
                }
            }
   
        }

        .article-info{
            padding-top: 116px;
            position: absolute;
            height: 100%;
            top: 0;
            right: 0;
            color: $dark;
            background-color: transparent;
            width: 402px;
            backdrop-filter: none;

            @media only screen and (max-width: $bp-xxl) {
                width: 360px;
            }
    
            @media only screen and (max-width: $bp-xl) {
                width: 320px;
            }
    
            @media only screen and (max-width: $bp-lg) {
                position: relative;
                padding-top: 0;
                width: 100%;
                height: auto;
                margin-top: 24px;
                background-color: $light-light;
            }

            &__inner{
                overflow: visible;

                @media only screen and (max-width: $bp-lg) {
                    overflow: hidden;
                }
            }

            &__content{
                position: sticky;
                top: 160px;
                border-left: 1px solid #D1D5DB;
                max-height: calc(100vh - 160px);
                overflow-y: auto;

                @media only screen and (max-width: $bp-lg) {
                    max-height: none;
                    overflow: initial;
                    position: static;
                    border-left: 0;
                }
            }

            &__block-title{
                color: #9CA3AF;
            }

            &__item{
                color: #1F2937;
                fill: #1F2937;

                &.link{
                    &:hover{
                        color: $blue-medium;
                        fill: $blue-medium;
                    }
                }
            }

            &__schedule{
                color: #6B7280;

                &:hover{
                    color: $dark;
                }
            }

            &__button{
                color: $dark;
                fill: $dark;
            }
        }
    }

    &--places &__inner > &__actions{
        position: absolute;
        top: 0;
        right: 0;
        width: 418px;
        z-index: 5;
        margin-top: 0;
        
        @media only screen and (max-width: $bp-xxl) {
            width: 376px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 336px;
        }

        @media only screen and (max-width: $bp-lg) {
            position: static;
            width: calc(100% + 16px);
            margin-top: 24px;
        }
    }

    &--places &__inner > &__actions &__action{
        flex-grow: 1;
    }

    &--projects {
        .article-intro{
            min-height: 550px;
            justify-content: center;

            &::after{
                width: 100vw;
                left: 50%;
                transform: translateX(-50%);
                background-color: rgba(21, 31, 109, 0.5);

                @media only screen and (max-width: $bp-lg) {
                    content: "";
                }
            }

            &__text{

            }

            &__img{
                position: absolute;
                width: 100vw;
                left: 50%;
                transform: translateX(-50%);

                @media only screen and (max-width: $bp-lg) {
                    margin: 0;
                    height: 100%;
                }
            }

            &__inner{
                height: 100%;

                @media only screen and (max-width: $bp-lg) {
                    padding: 24px 0;
                }
            }

            &__main{
                align-items: center;
                justify-content: center;
                height: 100%;
                margin-right: 0;

                @media only screen and (max-width: $bp-lg) {
                    align-items: center;
                    justify-content: flex-start;
                }
            }

            &__header{
                justify-content: center;
                margin-bottom: 16px;

                @media only screen and (max-width: $bp-lg) {
                    padding-bottom: 0;
                    margin-bottom: 0;
                    border-bottom: 0;
                    justify-content: space-between;
                }
            }

            &__date{
                color: #D1D5DB;
                padding-left: 16px;
                line-height: 32px;
                border-left: 1px solid #9CA3AF;

                @media only screen and (max-width: $bp-lg) {
                    border-left: 0;
                    order: 0;
                    margin-top: 0 !important;
                    width: auto !important;
                    border-top: 0 !important;
                    padding: 0 !important;
                    color: #fff;
                }
            }

            &__labels{

            }

            &__label{
                background-color: transparent !important;
                border: 0;
                fill: #D1D5DB;
                color: #D1D5DB;

                .icon{
                    @media only screen and (max-width: $bp-lg) {
                        width: 16px;
                        height: 16px;
                        margin-right: 5px;
                    }
                }

                &:first-of-type{
                    background-color: transparent !important;
                    fill: #D1D5DB;
                    color: #D1D5DB;
        
                    @media only screen and (max-width: $bp-lg) {
                        background-color: $orange;
                        color: #fff;
                    }
                }
            }

            &__title{
                display: flex;
                font-weight: 700;
                font-size: 60px;
                line-height: 120%;
                margin-top: 0;
                text-align: center;

                @media only screen and (max-width: $bp-xl) {
                    font-size: 52px;
                }
            
                @media only screen and (max-width: $bp-lg) {
                    font-size: 40px;
                    color: #fff;
                    margin-top: auto;
                    text-align: center;
                }

                @media only screen and (max-width: $bp-md) {
                    font-size: 34px;
                }
            }

            &__subtitle{
                display: flex;
                margin-top: 8px;
                color: #fff;
                text-align: center;

                @media only screen and (max-width: $bp-lg) {
                    margin-top: 4px;
                    text-align: center;
                }

                @media only screen and (max-width: $bp-md) {
                    font-size: 20px;
                    line-height: 32px;
                }
            }
        }
    }
    
