                font-weight: 700;
                font-size: 60px;
                line-height: 120%;
                margin-top: 0;
                text-align: center;

                @media only screen and (max-width: $bp-xl) {
                    font-size: 52px;
                }
            
                @media only screen and (max-width: $bp-lg) {
                    font-size: 40px;
                    color: #fff;
                    text-align: center;
                }

                @media only screen and (max-width: $bp-md) {
