    &--books {
        .article-intro{
            min-height: auto;
            padding: 0;
            position: static;
            display: flex;
            flex-direction: column;
    
            &::after{
                content: none;
            }

            &__main{
                margin-right: 0;
            }

            &__title{
                width: calc(100% - 442px);
                color: $blue;
                order: -1;
                margin-bottom: 16px;

                @media only screen and (max-width: $bp-xxl) {
                    width: calc(100% - 400px);
                }

                @media only screen and (max-width: $bp-xl) {
                    width: calc(100% - 350px);
                }

                @media only screen and (max-width: $bp-lg) {
                    width: 100%;
                    margin-top: 24px;
                }
            }

            &__img{
                justify-content: center;
                align-items: center;
    
                img{
                    object-fit: contain;
                    object-position: center;
                }
            }

            &__slider{
                width: calc(100% - 442px);

                @media only screen and (max-width: $bp-xxl) {
                    width: calc(100% - 400px);
                }

                @media only screen and (max-width: $bp-xl) {
                    width: calc(100% - 350px);
                }

                @media only screen and (max-width: $bp-lg) {
                    order: 2;
                    min-width: 100%;
                    width: auto;
                }
            }

            &__header{
                justify-content: space-between;
                margin-bottom: 0;

                @media only screen and (max-width: $bp-lg) {
                    flex-direction: column;
                    align-items: initial;
                    width: 100%;
                    padding-bottom: 0;
                    border-bottom: 0;
                }
            }

            &__date{
                font-weight: 400;
                margin-left: 16px;
                font-size: 16px;
                line-height: 28px;
                color: #6B7280;

                @media only screen and (max-width: $bp-lg) {
                    margin-left: 0;
                    padding-top: 12px;
                    margin-top: 12px;
                    border-top: 1px solid #D1D5DB;
                    font-size: 14px;
                    line-height: 22px;
                }
            }
        
            &__title{
                font-size: 34px;
                line-height: 125%;
                letter-spacing: -0.02em;
            
                @media only screen and (max-width: $bp-lg) {
                    font-size: 30px;
                    margin-bottom: 12px;
                }
            
                @media only screen and (max-width: $bp-md) {
                    font-size: 24px;
                    line-height: 32px;
                    margin-top: 0;
                }
            }
        
            &__subtitle{
                
            }
   
        }
    }

    &--books &__aside{
        position: absolute;
        height: calc(100% - 120px);
        top: 120px;
        z-index: 2;
        right: 0;
        margin-top: 0;

        @media only screen and (max-width: $bp-lg) {
            position: static;
        }
    }

    &--books &__inner > &__actions{
        position: absolute;
        top: 0;
        right: 0;
        width: 418px;
        z-index: 5;
        margin-top: 0;
        justify-content: flex-end;
        
        @media only screen and (max-width: $bp-xxl) {
            width: 376px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 336px;
        }

        @media only screen and (max-width: $bp-lg) {
            position: static;
            width: calc(100% + 16px);
            margin-top: 24px;
        }
    }

    &--books &__inner > &__actions &__action{
        @media only screen and (max-width: $bp-xl) {
            flex-grow: 1;
        }
    }
}

.article-intro{
    box-sizing: border-box;
    position: relative;
    display: flex;
    width: 100%;
    min-height: 400px;
    padding: 30px;
    color: #fff;

    @media only screen and (max-width: $bp-lg) {
        min-height: auto;
        padding: 0;
        color: $blue;
        flex-direction: column;
    }

    &::after{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        background-color: rgba(17, 24, 39, 0.5);

        @media only screen and (max-width: $bp-lg) {
            content: none;
        }
    }
    
    &__img {
        display: flex;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        @media only screen and (max-width: $bp-lg) {
            position: static;
            order: 2;
            height: 400px;
            margin-top: 24px;
            width: auto;
            min-width: 100%;
            margin: 24px -16px 0;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__slider{
        width: 100%;
        margin-top: 32px;
        
        @media only screen and (max-width: $bp-lg) {
            height: 300px;
            position: static;
            order: 2;
            margin-top: 24px;
            width: auto;
            min-width: 100%;
            margin: 24px -16px 0;
        }

        .slider{
            &__navigation {
                position: absolute;
                right: 32px;
                bottom: 32px;

                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }
            }
        }
    }

    &__slider &__img{
        position: static;
        height: 440px;
        margin: 0;

        @media only screen and (max-width: $bp-lg) {
            height: 300px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 268px;
        }
    }

    &__main{
        position: relative;
        z-index: 2;
        height: 100%;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        margin-right: auto;

        @media only screen and (max-width: $bp-lg) {
            order: 1;
            height: auto;
            margin-right: 0;
        }
    }

    &__header {
        display: flex;
        align-items: center;
        flex-wrap: wrap-reverse;
        margin-bottom: 24px;
        width: 100%;

        @media only screen and (max-width: $bp-lg) {
            flex-wrap: wrap;
            margin-bottom: 12px;
            padding-bottom: 12px;
            border-bottom: 1px solid #D1D5DB;
        }
    }

    &__labels {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-right: 8px;
        margin-bottom: -12px;
    }

    &__label {
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        height: 32px;
        padding: 0 8px;
        margin: 0 8px 12px 0;
        max-width: 100%;
        overflow: hidden;
        flex-shrink: 0;
        border: 1px solid #fff;
        text-decoration: none;
        border-radius: 50px;
        fill: #fff;
        color: #fff;
        font-weight: 600;
        font-size: 20px;
        line-height: 32px;
        transition: color 0.35s ease, border-color 0.35s ease;

        @media only screen and (max-width: $bp-xl) {
            font-size: 18px;
            line-height: 24px;
        }
        
        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
            height: 24px;
            padding: 0 6px;
            color: $orange;
            fill: $orange;
            border-color: $orange;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
        
        &:first-of-type{
            background-color: #fff;
            color: $blue;
            fill: $blue;

            @media only screen and (max-width: $bp-lg) {
                background-color: $orange;
                color: #fff;
            }
        }

        &:last-of-type,
        &:nth-last-child(2){
            flex-shrink: 1;
        }

        .icon{
            width: 24px;
            height: 24px;
            margin-right: 8px;
        }

        span{
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 100%;
            white-space: nowrap;
        }
    }

    &__date {

        @media only screen and (max-width: $bp-lg) {
            color: #6B7280;
            font-size: 14px;
            line-height: 22px;
        }
    }
    
    &__link{
        font-weight: 400;
        color: #1890FF; 
        transition: color 0.35s ease;
        text-decoration: none;

        &:hover{
            color: $blue;
        }
    }

    &__labels + &__date{
        @media only screen and (max-width: $bp-lg) {
            margin-top: 12px;
            width: 100%;
        }
    }

    &__uptitle{
        font-size: 24px;
        line-height: 32px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 20px;
            line-height: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
            line-height: 100%;
            margin-bottom: 8px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }

    &__title {
        margin-top: auto;

        @media only screen and (max-width: $bp-lg) {
            color: $blue;
            margin: 0;
        }
    }

    &__subtitle {
        margin-top: 16px;
        color: #D1D5DB;

        @media only screen and (max-width: $bp-lg) {
            margin: 8px 0 0;
            font-weight: 400;
            color: $blue;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 24px;
        }
    }

    &__info{
        width: 320px;
        margin-left: 48px;

        @media only screen and (max-width: $bp-lg) {
            order: 3;
            margin: 0;
            width: 100%;
            margin-top: 16px;

        }
    }
}

.article-info{
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: 100%;
    flex-shrink: 0;
    background: rgba(243, 244, 246, 0.2);
    backdrop-filter: blur(5px);
    color: $light;
    position: relative;
    z-index: 3;

    @media only screen and (max-width: $bp-lg) {
        height: auto;
        color: $dark;
        background-color: $light-light;
    }

    &__inner{
        display: flex;
        flex-direction: column;
        height: 100%;
        position: relative;
        overflow: hidden;
        transition: max-height 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            max-height: 218px;
        }

             
        &::after{
            display: block;
            width: 100%;
            height: calc(100% - 90px);
            position: absolute;
            bottom: 0;
            left: 0;
            z-index: 1;
            background: linear-gradient(180deg, rgba(243, 244, 246, 0) -80.56%, rgba(243, 244, 246, 0.1) -80.54%, #F3F4F6 100%);
            transition: opacity 0.35s ease;

            @media only screen and (max-width: $bp-lg) {
                content: "";
            }
        }

        &.is-active::after{
            opacity: 0;
            pointer-events: none;
        }
    }

    &__title {
        margin-bottom: 24px;
        
        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 24px;
        }
    }

    &__content {
        padding: 24px;

        @media only screen and (max-width: $bp-lg) {
            padding: 16px;
        }
    }

    &__block {
        display: flex;
        flex-direction: column;
        margin-bottom: 24px;

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__block-title {
        margin-bottom: 12px;
        color: #D1D5DB;
        font-size: 16px;
        line-height: 100%;

        @media only screen and (max-width: $bp-lg) {
            color: $light-medium;
            margin-bottom: 8px;
        }
    }

    &__item {
        display: inline-flex;
        align-items: center;
        color: $light;
        fill: $light;
        text-decoration: none;
        font-weight: 600;
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 12px;

        @media only screen and (max-width: $bp-lg) {
            color: $dark;
            fill: $dark;
            font-size: 16px;
            line-height: 28px;
            margin-bottom: 8px;
        }

        &:last-of-type{
            margin-bottom: 0;
        }

        span{
            margin-left: 8px;
        }

        .icon{
            width: 20px;
            height: 20px;
        }
    }

    &__item + &__map{
        margin-top: 12px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 8px;
        }
    }

    &__map{
        height: 200px;
        width: 100%;

        @media only screen and (max-width: $bp-lg) {
            height: 180px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 120px;
        }
    }

    &__profile {
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        font-weight: 600;
        font-size: 16px;
        line-height: 100%;
        text-decoration: none;
        padding: 8px;
        margin-bottom: 4px;

        &:last-of-type{
            margin-bottom: 8px;
        }
    }

    &__profile-img {
        display: flex;
        flex-shrink: 0;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 12px;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__schedule{
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        min-height: 28px;
        padding: 0 8px;
        margin-bottom: 12px;
        font-weight: 600;
        font-size: 18px;
        line-height: 24px;
        color: $light-light;
        transition: background-color 0.35s ease, color 0.35s ease;

        &:last-child{
            margin-bottom: 0;
        }

        &:hover{
            color: $light;
            background-color: #F3F4F6;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
            line-height: 28px;
            margin-bottom: 8px;
        }
    }

    &__button {
        color: $light;
        fill: $light;
        background: rgba(249, 250, 251, 0.15);
        border: 1px solid rgba(229, 231, 235, 0.5);
        margin-top: 24px;

        @media only screen and (max-width: $bp-lg) {
            background-color: $light;
            color: $dark;
            fill: $dark;
            border-color: #E5E7EB;
        }

        &:hover{
            color: $dark;
            fill: $dark
        }
    }

    &__profile + &__button{
        margin-top: 0;
    }

    &__more{
        position: absolute;
        bottom: 16px;
        left: 0;
        margin: 0 16px;
        width: calc(100% - 32px);
        z-index: 2;
        display: none;

        @media only screen and (max-width: $bp-lg) {

            &.is-visible{
                display: flex;
            }

            &.is-active{
                pointer-events: none;
                opacity: 0;
            }
        }
    }
}
