@import "@theme/vars.scss";

.ui{
    padding: 60px 0;
    // background-color: $dark;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    min-height: 100vh;

    &__inner{

    }

    &__block{
        border-bottom: 1px solid $blue;
        padding: 30px 0;
    }

    &__row{
        box-sizing: border-box;
        display: flex;
        flex-wrap: wrap;
        margin: 0 -20px -10px;
    }

    &__col{
        box-sizing: border-box;
        position: relative;
        max-width: 100%;
        flex-grow: 1;
        padding: 0 20px;
        margin-bottom: 20px;
        min-height: 50px;
    }
}