@import "@theme/vars.scss";

a,
button,
.button {
    cursor: pointer;
    padding: 0;
    background: none;
    border: none;
    outline: none;
}

.button {
    box-sizing: border-box;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 16px;
    height: 44px;
    text-decoration: none;
    font-weight: 600;
    font-size: 16px;
    line-height: 135%;
    background-color: $blue;
    color: $light;
    fill: $light;
    transition: color 0.35s ease, fill 0.35s ease, border-color 0.35s ease, background-color 0.35s ease, opacity 0.35s ease, box-shadow 0.35s ease;

    @media only screen and (max-width: $bp-lg) {
        height: 40px;
        font-size: 14px;
        padding: 0 12px;
    }

    @media only screen and (max-width: $bp-md) {
        height: 32px;
    }

    &__icon,
    .icon{
        flex-shrink: 0;
        width: 24px;
        height: 24px;
        
        @media only screen and (max-width: $bp-lg) {
            width: 20px;
            height: 20px;
        }     

        @media only screen and (max-width: $bp-md) {
            width: 16px;
            height: 16px;
        }
    }

    &__text{
        
    }

    &__text + &__icon{
        margin-left: 8px;

        @media only screen and (max-width: $bp-lg) {
            margin-left: 6px;
        }
    }

    &__icon + &__text{
        margin-left: 8px;

        @media only screen and (max-width: $bp-lg) {
            margin-left: 6px;
        }
    }

    &:hover{
        background-color: $blue-dark;
    }

    &:active{
        background-color: $blue-light;
    }

    &--light{
        background-color: $light;
        color: $dark-light;
        fill: $dark-light;
        border: 1px solid #E5E7EB;

        &:hover{
            background-color: $light-light;
        }

        &:active{
            background-color: #E5E7EB;
            border-color: #D1D5DB;
        }
    }

    &--ghost{
        background-color: transparent;
        color: #374151;
        fill: #374151;

        &:hover{
            background-color: rgba(221, 205, 255, 0.1);
        }

        &:active{
            background-color: rgba(215, 199, 255, 0.5);
        }
    }
    
    &--md{
        height: 40px;
    }

    &--sm{
        height: 32px;
    }

    &--md,
    &--sm {
        font-size: 14px;
    }

    &--md &__icon,
    &--sm &__icon {
        width: 16px;
        height: 16px;
    }

    &[disabled]{
        pointer-events: none;
        opacity: 0.5;
        background-color: #E5E7EB;
        color: $light;
        fill: $light;
    }

    &--ghost &[disabled]{
        pointer-events: none;
        opacity: 0.5;
    }

    &.jsLike{
        fill: transparent;
        stroke: $dark;

        &:hover{
            fill: $dark;
        }
    }

    &.is-favorite{
        background-color: #EC7D42;
        border-color: #EC7D42;
        color: $light;
        fill: $light;
        stroke: $light;
        --icon-opacity: 1;

        &:hover{
            background-color: #D3682E;
            border-color: #D3682E;
            fill: $light;
            stroke: $light;
        }

        &:active{
            background-color: #BA531A;
            border-color: #BA531A;
        }
    }

    &--icon{
        width: 44px;
        padding: 0;
        
        @media only screen and (max-width: $bp-lg) {
            width: 40px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 32px;
        }
    }

    &--icon#{&}--md{
        width: 40px;

        @media only screen and (max-width: $bp-lg) {
            width: 32px;
        }
    }

    &--icon#{&}--sm{
        width: 32px;
    }
    
    &--icon#{&}--round{
        border-radius: 50%;
    }
}