@import "@theme/vars.scss";

.calendar {

    .slider {
        display: flex;
        align-items: flex-end;

        &__wrapper{
            transition-timing-function: ease !important;
        }

        &__slide {
            box-sizing: border-box;
            display: flex;
            align-items: center;
            width: auto;
        }
    
        &__button {
            width: 32px;
            height: 68px;
            margin: 0;
            background-color: transparent;
            fill: $blue;
            border-radius: 0;

            @media only screen and (max-width: $bp-lg) {
                display: none;
            }

            &:hover{
                fill: $blue;
                background-color: $light;
            }
        }
    
        &__button-icon {
            width: 32px;
            height: 32px;
        }
    }
    
    &__month {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
  
        &>span{
            display: inline-flex;
            font-size: 24px;
            line-height: 100%;
            margin-bottom: 16px;
            opacity: 0.5;

                    
            @media only screen and (max-width: $bp-xl) {
                font-size: 22px;
            }

            @media only screen and (max-width: $bp-lg) {
                font-size: 20px;
                margin-bottom: 8px;
            }
            
            @media only screen and (max-width: $bp-md) {
                font-size: 18px;
            }
        }
    }

    &__days{
        display: flex;
        align-items: center;
    }

    &__day {
        box-sizing: border-box;
        cursor: pointer;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        min-width: 35px;
        padding: 8px;
        margin-right: 12px;
        text-transform: capitalize;
        color: $dark;
        transition: color 0.35s ease, background-color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            min-width: 30px;
            padding: 4px;
            margin-right: 8px;
        }

        &:hover{
            background-color: $light;
        }

        &.is-active,
        &.is-range {
            background-color: $blue;
            color: #fff;
        }

        &--weekend{
            color: #F87171;
        }

        &:last-of-type{
            margin-right: 0;
        }

        &>*{
            pointer-events: none;
        }

        strong{
            font-weight: 400;
            font-size: 32px;
            line-height: 100%;
            margin-bottom: 4px;

            @media only screen and (max-width: $bp-xl) {
                font-size: 28px;
            }

            @media only screen and (max-width: $bp-lg) {
                font-size: 24px;
            }
        }

        span{
            font-size: 16px;
            line-height: 100%;
            opacity: 0.8;

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
            }
        }
    }
}